function CustomTableRenderer(props) {
	const { children } = props;

	return (
		<div style={{ overflowX: "auto", marginTop: "20px", marginBottom: "20px" }}>
			<div style={{ display: "inline-block", margin: "1px" }}>
				<table>{children}</table>
			</div>
		</div>
	);
}
export default CustomTableRenderer;
export { CustomTableRenderer };
