export default "const getCookiesConsent = () => Number(localStorage.getItem(\"cookiesConsent\")) || \"0\";\r\n\r\nconst setCookiesConsent = (accepted) => localStorage.setItem(\"cookiesConsent\", accepted ? \"1\" : \"0\");\r\n\r\nconst getReadBlogPostIds = () => (localStorage.getItem(\"readBlogPostIds\") || \"\").split(\",\").filter((e) => e) || [];\r\n\r\nconst addReadBlogPostId = (postId) => {\r\n\tconst readBlogPostIds = getReadBlogPostIds();\r\n\tlocalStorage.setItem(\"readBlogPostIds\", Array.from(new Set([...readBlogPostIds, postId])).join(\",\"));\r\n};\r\n\r\nconst getBlogPostFontSizeLevel = () => Number(localStorage.getItem(\"fontSizeLevel\")) || 1;\r\n\r\nconst setBlogPostFontSizeLevel = (fontSizeLevel) => localStorage.setItem(\"fontSizeLevel\", fontSizeLevel);\r\n\r\nconst LocalStorageService = {\r\n\tgetCookiesConsent,\r\n\tsetCookiesConsent,\r\n\tgetReadBlogPostIds,\r\n\taddReadBlogPostId,\r\n\tgetBlogPostFontSizeLevel,\r\n\tsetBlogPostFontSizeLevel,\r\n};\r\n\r\nexport default LocalStorageService;\r\nexport {\r\n\tgetCookiesConsent,\r\n\tsetCookiesConsent,\r\n\tgetReadBlogPostIds,\r\n\taddReadBlogPostId,\r\n\tgetBlogPostFontSizeLevel,\r\n\tsetBlogPostFontSizeLevel,\r\n};\r\n";