export default "import { useState, useEffect } from \"react\";\r\nimport { HashRouter } from \"react-router-dom\";\r\nimport { HashLink } from \"react-router-hash-link\";\r\nimport { useSelector, useDispatch } from \"react-redux\";\r\nimport { Eportfolio } from \"./components/Eportfolio\";\r\nimport Particles from \"react-tsparticles\";\r\nimport { FullScreenMenu } from \"./components/global/FullScreenMenu\";\r\nimport { defaultConfig, snowConfig } from \"./config/particles\";\r\nimport { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground } from \"./components/global/button/AppButtons\";\r\nimport { NavMenuButton } from \"./components/global/button/NavMenuButton\";\r\nimport { CookiesConsent } from \"./components/global/CookiesConsent\";\r\nimport { ScrollUtils } from \"./utils\";\r\nimport { changeImagePopupSource } from \"./redux/action\";\r\n\r\nconst particlesConfig = new Date().getMonth() === 11 ? snowConfig : defaultConfig;\r\n\r\nfunction App(props) {\r\n\tconst { show: isImagePopupVisible } = useSelector((state) => state.imagePopupReducer);\r\n\r\n\tconst [isScrollToTopButtonVisible, setScrollToTopButtonVisible] = useState(false);\r\n\tconst [isNavMenuOpen, setNavMenuOpen] = useState(false);\r\n\r\n\tconst dispatch = useDispatch();\r\n\r\n\tuseEffect(() => {\r\n\t\tsetScrollToTopButtonVisible(ScrollUtils.isScrollToTopButtonVisible());\r\n\r\n\t\tconst escapeKeyListener = (e) => {\r\n\t\t\tif (e.keyCode === 27) {\r\n\t\t\t\te.preventDefault();\r\n\t\t\t\te.stopPropagation();\r\n\r\n\t\t\t\tsetNavMenuOpen(false);\r\n\r\n\t\t\t\tif (isImagePopupVisible) {\r\n\t\t\t\t\tdispatch(changeImagePopupSource({ show: false }));\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t};\r\n\r\n\t\tconst scrollEventListener = (e) => setScrollToTopButtonVisible(ScrollUtils.isScrollToTopButtonVisible());\r\n\r\n\t\tdocument.addEventListener(\"keydown\", escapeKeyListener);\r\n\t\tdocument.addEventListener(\"scroll\", scrollEventListener);\r\n\r\n\t\treturn () => {\r\n\t\t\tdocument.removeEventListener(\"keydown\", escapeKeyListener);\r\n\t\t\tdocument.removeEventListener(\"scroll\", scrollEventListener);\r\n\t\t};\r\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\r\n\t}, [isImagePopupVisible]);\r\n\r\n\treturn (\r\n\t\t<>\r\n\t\t\t<HashRouter>\r\n\t\t\t\t<HashLink to=\"#top\" smooth replace>\r\n\t\t\t\t\t<ScrollToTopButton\r\n\t\t\t\t\t\ticon=\"chevron up\"\r\n\t\t\t\t\t\tcompact\r\n\t\t\t\t\t\tcircular\r\n\t\t\t\t\t\tsize=\"big\"\r\n\t\t\t\t\t\tcolor=\"yellow\"\r\n\t\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t\topacity: !isScrollToTopButtonVisible || isNavMenuOpen || isImagePopupVisible ? 0 : 1,\r\n\t\t\t\t\t\t\tpointerEvents:\r\n\t\t\t\t\t\t\t\t!isScrollToTopButtonVisible || isNavMenuOpen || isImagePopupVisible ? \"none\" : false,\r\n\t\t\t\t\t\t}}\r\n\t\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\r\n\t\t\t\t\t/>\r\n\t\t\t\t</HashLink>\r\n\r\n\t\t\t\t<CookiesConsent />\r\n\r\n\t\t\t\t<NavMenuButton\r\n\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\r\n\t\t\t\t\tshow={!isImagePopupVisible}\r\n\t\t\t\t\taction={() => setNavMenuOpen(!isNavMenuOpen)}\r\n\t\t\t\t/>\r\n\t\t\t\t<NavMenuButtonShadow style={{ opacity: isImagePopupVisible ? 0 : 1 }} navMenuOpen={isNavMenuOpen} />\r\n\r\n\t\t\t\t<ButtonsBackground\r\n\t\t\t\t\tid=\"nav-menu-buttons-background\"\r\n\t\t\t\t\tshow={!isImagePopupVisible}\r\n\t\t\t\t\tnavMenuOpen={isNavMenuOpen}\r\n\t\t\t\t/>\r\n\r\n\t\t\t\t<FullScreenMenu navMenuOpen={isNavMenuOpen} menuItemCallback={() => setNavMenuOpen(false)} />\r\n\r\n\t\t\t\t<div\r\n\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\twidth: \"100%\",\r\n\t\t\t\t\t\topacity: isNavMenuOpen ? 0 : 1,\r\n\t\t\t\t\t\ttransition: \"250ms\",\r\n\t\t\t\t\t}}\r\n\t\t\t\t>\r\n\t\t\t\t\t<Eportfolio historyChangeCallback={() => setScrollToTopButtonVisible(false)} />\r\n\t\t\t\t</div>\r\n\t\t\t</HashRouter>\r\n\r\n\t\t\t<Particles className=\"particles-wrapper\" params={particlesConfig} />\r\n\t\t</>\r\n\t);\r\n}\r\n\r\nexport default App;\r\nexport { App };\r\n";