import { memo, useState } from "react";
import { useDispatch } from "react-redux";
import { changeImagePopupSource } from "../../../../redux/action";

function Avatar(props) {
	const [queryString, setQueryString] = useState("");

	const dispatch = useDispatch();

	const src = "avatar.jpg";

	return (
		<div style={{ paddingTop: "3em" }}>
			<picture>
				<source type="image/webp" srcSet={encodeURIComponent(src.replace(/\.(jpg|png|gif)/, ".webp"))} />
				<img
					src={`${src}${queryString}`}
					alt="My profile pic"
					draggable="false"
					className="ui large circular centered spaced image"
					onContextMenu={(e) => e.preventDefault()}
					onClick={() =>
						dispatch(
							changeImagePopupSource({
								images: [src],
								activeIndex: 0,
							})
						)
					}
					onError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}
					style={{
						cursor: "pointer",
						padding: "8px",
						background: `
						radial-gradient(
							circle at 50% 0,
							rgba(255, 0, 0, 0.375),
							rgba(255, 0, 0, 0) 70.71%
						),
						radial-gradient(
							circle at 6.7% 75%,
							rgba(0, 0, 255, 0.375),
							rgba(0, 0, 255, 0) 70.71%
						),
						radial-gradient(
							circle at 93.3% 75%,
							rgba(0, 255, 0, 0.375),
							rgba(0, 255, 0, 0) 70.71%
						)
						white
					`,
					}}
				/>
			</picture>
		</div>
	);
}

const memoComponent = memo(Avatar, (prev, next) => true);
export default memoComponent;
export { memoComponent as Avatar };
