import { memo, Fragment } from "react";
import { Helmet } from "react-helmet";
import { Header, Divider } from "semantic-ui-react";
import { Body } from "../../Styles";
import { Project } from "../Project";
import { hobbyProjects } from "../../../config";

function HobbyProject(props) {
	return (
		<Body>
			<Helmet>
				<title>My Hobby Projects | {process.env.REACT_APP_TITLE}</title>
			</Helmet>

			<Header size="huge" style={{ cursor: "default" }}>
				My Hobby Projects
			</Header>
			{(hobbyProjects || [])
				.map(({ projectId, ...rest }) => ({ ...rest, path: `/hobbyProject/${projectId}` }))
				.map((project, i, projects) => (
					<Fragment key={i}>
						<Divider section />
						<Project detail={project} inverted={(projects.length - i) % 2 === 1} />
					</Fragment>
				))}
		</Body>
	);
}

const memoComponent = memo(HobbyProject, (prev, next) => true);
export default memoComponent;
export { memoComponent as HobbyProject };
