import { memo, useState } from "react";
import { useParams, useLocation, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { Helmet } from "react-helmet";
import Lazyload from "react-lazyload";
import { Segment, Header, Label, Button, Divider, Icon, Responsive } from "semantic-ui-react";
import styled from "styled-components";
import { PageNotFound } from "./PageNotFound";
import { Body, NoSelect, GradientText } from "../../Styles";
import moment from "moment";
import { responsiveMaxWidth, responsiveMinWidth, workProjects, hobbyProjects } from "../../../config";
import { changeImagePopupSource } from "../../../redux/action";
import find from "lodash/find";

const CustomSegment = styled(Segment)`
	background-color: rgba(255, 255, 255, 0.25) !important;

	img {
		position: relative;
		max-width: 50%;
		max-height: 400px;
		display: block;
		margin: 20px auto;
		border-radius: 10px;
		box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
		background-size: 200% 200%;
		transition: transform 400ms cubic-bezier(0, 1.2, 0.2, 1.5);
		cursor: zoom-in;
	}
`;

function ProjectDetail(props) {
	const { projectType, backPath } = props;
	const { projectId } = useParams();
	const project = find([...workProjects, ...hobbyProjects], { projectId });
	const {
		projectName,
		projectNature,
		year,
		images,
		position,
		contractPeriod,
		workLocation,
		teamSize,
		descriptionOverview,
		descriptionContinued,
		description,
	} = project || {};

	const colors = ["#b71c1c", "#0d47a1", "#1b5e20", "#e65100", "#3e2723"];
	const yearColor = Number.isInteger(year) ? colors[(moment().year() % year) % colors.length] : "#212121";

	const [hoverImageIndex, setHoverImageIndex] = useState(null);
	const [queryString, setQueryString] = useState("");

	const location = useLocation();
	const dispatch = useDispatch();

	const navigate = useNavigate();

	const backToAllProjectsButton = (
		<span
			onClick={() => {
				if (window.history?.length >= 2 && location?.state?.fromAllProjects) {
					navigate(-1);
				} else {
					navigate(backPath);
				}
			}}
		>
			<Responsive
				as={Button}
				minWidth={responsiveMinWidth}
				basic
				compact
				color="pink"
				size="medium"
				content={
					<>
						Back to <b>{projectType || ""}</b>
					</>
				}
				icon="arrow left"
				labelPosition="left"
			/>
			<Responsive maxWidth={responsiveMaxWidth}>
				<Button basic compact color="pink" size="medium" icon="arrow left" content="Back" />
			</Responsive>
		</span>
	);

	return (
		<Body>
			<Helmet>
				<title>
					{project ? projectName : "404 Not Found"} | {projectType || "Project"} |{" "}
					{process.env.REACT_APP_TITLE}
				</title>
			</Helmet>

			{project ? (
				<>
					{backToAllProjectsButton}

					<Header size="huge" style={{ cursor: "default" }}>
						{projectName || ""}
					</Header>
					<Divider section />

					<CustomSegment style={{ padding: "3em 5vw" }}>
						<NoSelect>
							<Label.Group>
								<Label
									tag
									size="large"
									style={{
										color: "white",
										backgroundColor: yearColor,
									}}
								>
									{Number.isInteger(year) ? "Year " + year : year || "Others"}
								</Label>
								<Label tag size="large" color="teal">
									{projectNature || ""}
								</Label>
							</Label.Group>
						</NoSelect>

						<GradientText style={{ paddingTop: "2em" }}>
							{position && (
								<>
									Position: {position}
									<br />
								</>
							)}
							{contractPeriod && (
								<>
									Contract period: {contractPeriod}
									<br />
								</>
							)}
							{workLocation && (
								<>
									Work location: {workLocation}
									<br />
								</>
							)}
							{teamSize && (
								<>
									Team size: {teamSize}
									<br />
								</>
							)}

							{(position || contractPeriod || workLocation || teamSize) && <br />}

							{description || (
									<>
										{descriptionOverview}
										<br />
										<br />
										{descriptionContinued}
									</>
								) ||
								""}
						</GradientText>

						{images && images.length > 0 && (
							<>
								<Divider horizontal section style={{ paddingBottom: "2em" }}>
									<Header as="h5" style={{ opacity: 0.5 }}>
										<Icon name="photo" />
										Screenshots / Photos
									</Header>
								</Divider>

								{images.map((image, i) => (
									<Lazyload key={i} once height="400px" offset={400}>
										<picture>
											<source
												type="image/webp"
												srcSet={encodeURIComponent(
													image.replace(/\.(jpg|png|gif)/, "-crop.webp")
												)}
											/>
											<img
												src={`${image}${queryString}`}
												alt={image.replace(/\.(jpg|png|gif)/, "")}
												draggable={false}
												onContextMenu={(e) => e.preventDefault()}
												onMouseEnter={() => setHoverImageIndex(i)}
												onMouseOver={() => setHoverImageIndex(i)}
												onMouseLeave={() => setHoverImageIndex(null)}
												onTouchStart={() => setHoverImageIndex(i)}
												onTouchEnd={() => setHoverImageIndex(null)}
												onTouchCancel={() => setHoverImageIndex(null)}
												onClick={() => {
													setHoverImageIndex(null);
													dispatch(
														changeImagePopupSource({
															images,
															activeIndex: i,
														})
													);
												}}
												onError={() =>
													setTimeout(() => setQueryString(`?${Math.random()}`), 1000)
												}
												style={{
													...(i === hoverImageIndex && {
														transform: "scale(1.2)",
														boxShadow: "0 0 30px rgba(0, 0, 0, 1)",
														zIndex: 1,
													}),
													...(hoverImageIndex === null ||
														(i !== hoverImageIndex && {
															filter: "brightness(0.75) blur(2px)",
														})),
												}}
											/>
										</picture>
									</Lazyload>
								))}
							</>
						)}
					</CustomSegment>

					<Divider section />
					{backToAllProjectsButton}
				</>
			) : (
				<PageNotFound />
			)}
		</Body>
	);
}

const memoComponent = memo(ProjectDetail, (prev, next) => prev.projectId === next.projectId);
export default memoComponent;
export { memoComponent as ProjectDetail };
