import { memo, useState } from "react";
import styled from "styled-components";
import { NoSelect } from "../../../Styles";

const HightlightedTextStyle = styled(NoSelect)`
	display: inline-block;
	color: ${({ hovered }) => (hovered ? "purple" : "gold")};
	background: ${({ hovered }) => (hovered ? "gold" : "purple")};
	${({ hovered }) => (hovered ? "cursor: pointer;" : "")}
	font-size: 1.25em;
	font-weight: bold;
	padding: 2px 8px 2px 8px;
	margin: 0 3px 0 3px;
	border-radius: 5px;
	transition: 250ms;
	-webkit-box-reflect: below 0em -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(255, 255, 255, 0.1)));
`;

const openLink = (url) => window.open(url, "_blank", "noopener");

function HightlightedText(props) {
	const { url } = props;

	const [isHovered, setHovered] = useState(false);

	return (
		<HightlightedTextStyle
			{...props}
			hovered={isHovered}
			onMouseEnter={() => setHovered(true)}
			onMouseOver={() => setHovered(true)}
			onMouseLeave={() => setHovered(false)}
			onTouchStart={() => setHovered(true)}
			onTouchEnd={() => setHovered(false)}
			onTouchCancel={() => setHovered(false)}
			onClick={() => {
				setHovered(false);
				openLink(url);
			}}
		/>
	);
}

const memoComponent = memo(HightlightedText, (prev, next) => true);
export default memoComponent;
export { memoComponent as HightlightedText };
