export default "import { memo, useState, useEffect } from \"react\";\r\nimport styled from \"styled-components\";\r\n\r\nconst NavMenuButtonWrapper = styled(({ hovered, navMenuOpen, ...props }) => <div {...props} />)`\r\n\twidth: 40px;\r\n\theight: 40px;\r\n\tbottom: 20px;\r\n\tright: 20px;\r\n\tposition: fixed;\r\n\tmargin-right: 4.5px;\r\n\tz-index: 1002;\r\n\tborder-radius: 10px;\r\n\tcursor: pointer;\r\n\ttransition: all 250ms, right 0ms !important;\r\n\tbackground: linear-gradient(-45deg, #ffa63d, #ff3d77, #338aff, #3cf0c5);\r\n\tbackground-size: 600%;\r\n\tanimation: animated-button 16s linear infinite !important;\r\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;\r\n\r\n\t${({ hovered }) =>\r\n\t\thovered\r\n\t\t\t? `\r\n\tfilter: brightness(0.8);\r\n\r\n\tspan.middle {\r\n\t\topacity: ${({ navMenuOpen }) => (navMenuOpen ? 0 : 1)};\r\n\t}\r\n`\r\n\t\t\t: \"\"}\r\n\r\n\t@keyframes animated-button {\r\n\t\t0% {\r\n\t\t\tbackground-position: 0% 50%;\r\n\t\t}\r\n\t\t50% {\r\n\t\t\tbackground-position: 100% 50%;\r\n\t\t}\r\n\t\t100% {\r\n\t\t\tbackground-position: 0% 50%;\r\n\t\t}\r\n\t}\r\n\r\n\t@media (hover: hover) {\r\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\r\n\t}\r\n`;\r\n\r\nconst NavMenuButtonLine = styled(({ navMenuOpen, ...props }) => <span {...props} />)`\r\n\tbackground: white;\r\n\twidth: 24px;\r\n\theight: 4px;\r\n\tposition: absolute;\r\n\ttop: 9px;\r\n\tleft: 8px;\r\n\ttransition: 0.35s ease;\r\n\r\n\t&:nth-of-type(1) {\r\n\t\ttransform: ${({ navMenuOpen }) => (navMenuOpen ? \"translateY(9px) translateX(0) rotate(45deg)\" : \"\")};\r\n\t}\r\n\r\n\t&:nth-of-type(2) {\r\n\t\ttop: 18px;\r\n\t\t${({ navMenuOpen }) => (navMenuOpen ? \"opacity: 0;\" : \"\")}\r\n\t}\r\n\r\n\t&:nth-of-type(3) {\r\n\t\ttop: 27px;\r\n\t\ttransform: ${({ navMenuOpen }) => (navMenuOpen ? \"translateY(-9px) translateX(0) rotate(-45deg)\" : \"\")};\r\n\t}\r\n`;\r\n\r\nfunction NavMenuButton(props) {\r\n\tconst { navMenuOpen, show, action } = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\tuseEffect(() => {\r\n\t\tdocument.body.style.overflow = navMenuOpen ? \"hidden\" : null;\r\n\t}, [navMenuOpen]);\r\n\r\n\treturn (\r\n\t\t<NavMenuButtonWrapper\r\n\t\t\thovered={isHovered}\r\n\t\t\tnavMenuOpen={navMenuOpen}\r\n\t\t\tstyle={{\r\n\t\t\t\topacity: show ? 1 : 0,\r\n\t\t\t\t...(!show && { pointerEvents: \"none\" }),\r\n\t\t\t}}\r\n\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\tonClick={() => {\r\n\t\t\t\tsetHovered(false);\r\n\t\t\t\tif (typeof action === \"function\") {\r\n\t\t\t\t\taction();\r\n\t\t\t\t}\r\n\t\t\t}}\r\n\t\t>\r\n\t\t\t<NavMenuButtonLine className=\"top\" navMenuOpen={navMenuOpen} />\r\n\t\t\t<NavMenuButtonLine className=\"middle\" navMenuOpen={navMenuOpen} />\r\n\t\t\t<NavMenuButtonLine className=\"bottom\" navMenuOpen={navMenuOpen} />\r\n\t\t</NavMenuButtonWrapper>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(\r\n\tNavMenuButton,\r\n\t(prev, next) => prev.navMenuOpen === next.navMenuOpen && prev.show === next.show && prev.action === next.action\r\n);\r\nexport default memoComponent;\r\nexport { memoComponent as NavMenuButton };\r\n";