import { memo } from "react";
import { Grid, Icon, Header, Divider, Responsive } from "semantic-ui-react";
import { ContactItem } from "./ContactItem";
import { Body, NoSelect } from "./../Styles";
import { responsiveMaxWidth, responsiveMinWidth } from "../../config";
import preval from "preval.macro";
import GitInfo from "react-git-info/macro";
import moment from "moment";

function SiteFooter(props) {
	const currentYear = new Date().getFullYear();
	const lastUpdatedTime = preval`module.exports = new Date().getTime();`;

	const gitInfo = GitInfo();

	return (
		<div className="site-footer">
			<Body>
				<Divider horizontal section>
					<Header as="h5" style={{ opacity: 0.5 }}>
						<Icon name="react" />
						Reach Me Now!
					</Header>
				</Divider>
				<Grid stackable padded columns={3}>
					<Grid.Column>
						<ContactItem
							href="tel:+85263301333"
							icon="phone"
							iconRotation="clockwise"
							color="teal"
							header="Mobile"
							subHeader="6330 1333"
						/>
					</Grid.Column>
					<Grid.Column>
						<ContactItem
							href="mailto:michaelboyboy@gmail.com"
							icon="mail"
							color="red"
							header="Email"
							subHeader="michaelboyboy@gmail.com"
						/>
					</Grid.Column>
					<Grid.Column>
						<ContactItem
							href="https://hk.linkedin.com/in/mickchung"
							icon="linkedin"
							color="blue"
							header="LinkedIn"
							subHeader="hk.linkedin.com/in/mickchung"
						/>
					</Grid.Column>
				</Grid>

				<Divider />

				{[
					{
						props: {
							maxWidth: responsiveMaxWidth,
						},
						style: {
							div: {
								fontSize: "2.75vw",
							},
						},
					},
					{
						props: {
							minWidth: responsiveMinWidth,
						},
						style: {
							lastUpdatedOn: {
								float: "right",
							},
						},
					},
				].map(({ props = {}, style = {} }, i) => (
					<Responsive key={i} {...props}>
						<div style={{ color: "#4ba3c7", ...(style.div || {}) }}>
							<NoSelect style={{ display: "inline-block" }}>
								Copyright © {currentYear} Chung Cheuk Hang Michael. All rights reserved.
							</NoSelect>
							<NoSelect style={{ display: "inline-block", ...(style.lastUpdatedOn || {}) }}>
								Last Updated On:{" "}
								<span
									title={moment(lastUpdatedTime).format("D MMM YYYY HH:mm:ss")}
									style={{ cursor: "help" }}
								>
									{moment(lastUpdatedTime).format("D MMM YYYY")}
								</span>{" "}
								/{" "}
								<span title={gitInfo.commit.hash} style={{ cursor: "help" }}>
									{gitInfo.commit.shortHash}
								</span>
							</NoSelect>
						</div>
					</Responsive>
				))}

				<Divider hidden />
			</Body>
		</div>
	);
}

const memoComponent = memo(SiteFooter, (prev, next) => true);
export default memoComponent;
export { memoComponent as SiteFooter };
