import { Fragment, memo, useState } from "react";
import { useDispatch } from "react-redux";
import { Helmet } from "react-helmet";
import { Header, Grid, Divider } from "semantic-ui-react";
import { HightlightedText } from "./HightlightedText";
import { Body, NormalText } from "../../../Styles";
import { changeImagePopupSource } from "../../../../redux/action";

const personalityConfigs = [
	{
		title: (
			<>
				Me Today (2021) <span style={{ fontWeight: "normal" }}>😎</span>
			</>
		),
		personalities: ["I", "N", "T", "J", "-T"],
		isActive: true,
	},
	{ title: "Me Back in 2019", personalities: ["I", "S", "T", "J", "-A"] },
];

const personalityCombinationDescriptions = {
	"INTJ-T": "I question everything!",
	"ISTJ-A": "Logic means a lot to me!",
};

const personalityNames = {
	I: "introverted",
	E: "extraverted",
	N: "intuitive",
	S: "observant",
	T: "thinking",
	F: "feeling",
	J: "judging",
	P: "prospecting",
	"-A": "assertive",
	"-T": "turbulent",
};

const personalityLinks = {
	I: "https://www.16personalities.com/articles/mind-introverted-vs-extraverted",
	E: "https://www.16personalities.com/articles/mind-introverted-vs-extraverted",
	N: "https://www.16personalities.com/articles/energy-intuitive-vs-observant",
	S: "https://www.16personalities.com/articles/energy-intuitive-vs-observant",
	T: "https://www.16personalities.com/articles/nature-thinking-vs-feeling",
	F: "https://www.16personalities.com/articles/nature-thinking-vs-feeling",
	J: "https://www.16personalities.com/articles/tactics-judging-vs-prospecting",
	P: "https://www.16personalities.com/articles/tactics-judging-vs-prospecting",
	"-A": "https://www.16personalities.com/articles/identity-assertive-vs-turbulent",
	"-T": "https://www.16personalities.com/articles/identity-assertive-vs-turbulent",
};

const personalityDescriptions = {
	I: "I feel energetic and comfortable when work independently. I am self-reflective and studious.",
	E: "",
	N: "I like thinking in different dimensions and exploring possibilities. I can provide new directions when necessary.",
	S: "I am pragmatic so I see things by looking at how to make them in reality. I often focus on details just like every bit of this website: fonts, colours and backgrounds.",
	T: "I make decision based on truths and principles and by analyzing the pros and cons. As a result, I enjoy technical fields where logic is important to finding the solution.",
	F: "",
	J: "I am task-oriented and I like to have things well-organized and planned.",
	P: "",
	"-A": "I am confident and do not worry too much.",
	"-T": "I am eager to improve my skills. I am also a perfectionist.",
};

function Personality(props) {
	const [queryString, setQueryString] = useState("");

	const dispatch = useDispatch();

	const constructPersonalitySection = (title, personalities, isActive) => {
		const imageSrc = `personality-test-result-${personalities.join("").replaceAll("-", "").toLowerCase()}.png`;
		const url = `https://www.16personalities.com/${personalities
			.slice(0, 4)
			.map((e) => e.toLowerCase())
			.join("")}-personality`;

		return (
			<>
				<Header size="large" style={{ cursor: "default" }}>
					{title}
				</Header>

				<div style={{ ...(!isActive && { opacity: 0.5 }) }}>
					<Grid stackable>
						<Grid.Column width={6}>
							<picture>
								<source
									type="image/webp"
									srcSet={encodeURIComponent(imageSrc.replace(/\.(jpg|png|gif)/, ".webp"))}
								/>
								<img
									src={`${imageSrc}${queryString}`}
									alt="Personality test result"
									draggable="false"
									className="ui bordered rounded image"
									onContextMenu={(e) => e.preventDefault()}
									onClick={() =>
										dispatch(
											changeImagePopupSource({
												images: [imageSrc],
												activeIndex: 0,
											})
										)
									}
									onError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}
									style={{ cursor: "pointer" }}
								/>
							</picture>
						</Grid.Column>
						<Grid.Column width={10}>
							<NormalText>
								I am <HightlightedText url={url}>{personalities.join("")}</HightlightedText>.{" "}
								{personalityCombinationDescriptions[personalities.join("")]}
								{personalities.map((personality, i) => (
									<Fragment key={i}>
										<br />
										<br />I am a
										<HightlightedText url={personalityLinks[personality]}>
											{personalityNames[personality]} ({personality})
										</HightlightedText>
										person - {personalityDescriptions[personality]}
									</Fragment>
								))}
							</NormalText>
						</Grid.Column>
					</Grid>
				</div>
			</>
		);
	};

	return (
		<Body>
			<Helmet>
				<title>Who I am | {process.env.REACT_APP_TITLE}</title>
			</Helmet>

			<Header size="huge" style={{ cursor: "default" }}>
				Who I am
			</Header>
			<Divider section />

			{personalityConfigs.map(({ title, personalities, isActive }, i) => (
				<Fragment key={i}>
					{constructPersonalitySection(title, personalities, isActive)}
					{i < personalityConfigs.length - 1 && <Divider section />}
				</Fragment>
			))}
		</Body>
	);
}

const memoComponent = memo(Personality, (prev, next) => true);
export default memoComponent;
export { memoComponent as Personality };
