export default "import { useCallback } from \"react\";\r\nimport { useLocation } from \"react-router-dom\";\r\nimport { HashLink } from \"react-router-hash-link\";\r\nimport { Header, Icon, Responsive } from \"semantic-ui-react\";\r\nimport isEmpty from \"lodash/isEmpty\";\r\nimport filter from \"lodash/filter\";\r\nimport { ScrollUtils } from \"../../../../../utils\";\r\nimport { responsiveMinWidth } from \"../../../../../config\";\r\n\r\nfunction Heading(props) {\r\n\tconst { anchorRef, level, children } = props;\r\n\r\n\tconst location = useLocation();\r\n\r\n\tconst ref = useCallback(\r\n\t\t(node) => {\r\n\t\t\tif (node && location.hash && anchorRef === decodeURIComponent(location.hash.replace(/#/, \"\"))) {\r\n\t\t\t\tScrollUtils.scrollToElement(node, 500);\r\n\t\t\t}\r\n\t\t},\r\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\r\n\t\t[]\r\n\t);\r\n\r\n\treturn (\r\n\t\t<Header as={\"h\" + level} className={`header-with-anchor header-with-anchor-${level}`}>\r\n\t\t\t<HashLink ref={ref} id={anchorRef} to={\"#\" + anchorRef} smooth replace>\r\n\t\t\t\t<Responsive as={Icon} minWidth={responsiveMinWidth} name=\"linkify\" />\r\n\t\t\t</HashLink>\r\n\t\t\t{children}\r\n\t\t</Header>\r\n\t);\r\n}\r\n\r\nfunction CustomHeadingRenderer(props) {\r\n\tconst { tocArray, level, children = [] } = props;\r\n\r\n\tconst headingText = children.map((e) => (typeof e === \"string\" ? e : e.props.children.join(\"\"))).join(\"\");\r\n\tconst anchorRef = isEmpty(tocArray) ? null : (filter(tocArray, { content: headingText })[0] || {}).slug;\r\n\r\n\treturn (\r\n\t\t<Heading anchorRef={anchorRef} level={level}>\r\n\t\t\t{children}\r\n\t\t</Heading>\r\n\t);\r\n}\r\nexport default CustomHeadingRenderer;\r\nexport { CustomHeadingRenderer };\r\n";