import styled from "styled-components";
import ReactMarkdown from "react-markdown";

const FONT_SIZE_INCREMENT = 2;
const FONT_SIZE_MIN = 14;

const CustomMarkdown = styled(({ fontSizeLevel, ...props }) => <ReactMarkdown {...props} />)`
	word-wrap: break-word;

	.paragraph {
		font-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;
		line-height: 1.75em !important;
		margin-bottom: 1em !important;
	}

	li {
		font-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;
		line-height: 1.75em !important;
	}

	pre {
		overflow: auto;
		border-radius: 5px;
		padding: 20px;
		color: #455a64;
		background-color: rgba(144, 164, 174, 0.2);
		border: 1px solid #90a4ae;
		line-height: 1.2em;
		transition: 250ms;
		font-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;
	}

	*:not(pre):not(code) > code {
		color: #d81b60;
		background-color: rgba(27, 31, 35, 0.05);
		border-radius: 3px;
		padding: 0.2em 0.4em;
		transition: 250ms;
	}

	span.react-syntax-highlighter-line-number {
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		-webkit-user-drag: none;
		-khtml-user-drag: none;
		-moz-user-drag: none;
		-ms-user-drag: none;
		user-drag: none;
	}

	hr {
		margin: 20px auto;
		border: 1px solid rgba(144, 164, 174, 0.5);
	}

	img {
		position: relative;
		max-width: 50%;
		max-height: 400px;
		display: block;
		margin: 20px auto;
		padding: 3px;
		border-radius: 15px;
		box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
		background: linear-gradient(to right, red, orange, yellow);
		background-size: 200% 200%;
		transition: 400ms cubic-bezier(0, 1.2, 0.2, 1.5);
		animation: animated-border 3s ease infinite;
		cursor: zoom-in;
	}

	@keyframes animated-border {
		0% {
			background-position: 0% 0%;
		}

		50% {
			background-position: 100% 100%;
		}

		100% {
			background-position: 0% 0%;
		}
	}

	blockquote {
		font-style: italic;
		opacity: 0.5;
		padding-left: 25px;
		border-left: 5px solid rgba(41, 182, 246, 0.5);
		transition: 250ms;
	}

	ul,
	ol {
		padding-inline-start: 20px;
	}

	table {
		border-collapse: collapse;
		border-style: hidden;
		border-radius: 5px !important;
		box-shadow: 0 0 0 1px #90a4ae;
	}

	thead th {
		background-color: rgba(144, 164, 174, 0.25);
	}
	tr:nth-child(even) td {
		background-color: rgba(144, 164, 174, 0.05);
	}

	th:first-child {
		border-top-left-radius: 5px;
	}
	th:last-child {
		border-top-right-radius: 5px;
	}
	tr:last-child td:first-child {
		border-bottom-left-radius: 5px;
	}
	tr:last-child td:last-child {
		border-bottom-right-radius: 5px;
	}

	th,
	td {
		border: 1px solid #90a4ae;
		padding: 8px;
		line-height: 1.75em !important;
		font-size: ${({ fontSizeLevel = 0 }) => fontSizeLevel * FONT_SIZE_INCREMENT + FONT_SIZE_MIN}px !important;
	}

	@media (hover: hover) {
		pre:hover {
			background-color: rgba(224, 224, 224, 0.4);
		}

		*:not(pre):not(code) > code:hover {
			background-color: rgba(236, 64, 122, 0.15);
		}

		blockquote:hover {
			border-radius: 0 15px 15px 0;
			background-color: rgba(144, 164, 174, 0.25);
		}
	}
`;
export default CustomMarkdown;
export { CustomMarkdown };
