import { memo, useState } from "react";
import { Responsive } from "semantic-ui-react";
import styled from "styled-components";
import Typewriter from "typewriter-effect";
import { responsiveMaxWidth, responsiveMinWidth } from "../../../../config";

const CONSOLE_LOG_PREFIX = "console.log('";
const CONSOLE_LOG_PREFIX_MOBILE = "console.log<br />('";
const CONSOLE_LOG_SUFFIX = "')";

const MacTerminal = styled.div`
	text-align: center;
	width: 100%;
	height: 25px;
	background-color: #dad9d9;
	margin: 0 auto;
	font-family: monospace;
	padding: auto;
	float: none;
	border-radius: 5px 5px 0 0;
	cursor: move;

	.macos-terminal-button {
		display: block;
		position: absolute;
		border-radius: 100%;
		width: 15px;
		height: 15px;
		top: 19px;
		margin: 0 auto;
		cursor: pointer;
		transition: 100ms;

		&:active {
			filter: brightness(1.1);
		}
	}

	.macos-terminal-button.red {
		background-color: #ec6559;
		left: 10px;
	}

	.macos-terminal-button.yellow {
		background-color: #e0c04c;
		left: 32.5px;
	}

	.macos-terminal-button.green {
		background-color: #72bf47;
		left: 55px;
	}

	.macos-terminal-button-red-icon {
		position: absolute;
		width: 68%;
		height: 8%;
		left: 16%;
		top: 46%;
		border-radius: 25px;
		background-color: #975513;
	}

	.macos-terminal-button-red-icon-1 {
		transform: rotate(45deg);
	}

	.macos-terminal-button-red-icon-2 {
		transform: rotate(-45deg);
	}

	.macos-terminal-button-yellow-icon {
		position: absolute;
		width: 68%;
		height: 8%;
		left: 16%;
		top: 46%;
		border-radius: 25px;
		background-color: #975513;
	}

	.macos-terminal-button-green-icon {
		position: absolute;
		border-top: solid 30px transparent;
		border-right: solid 30px transparent;
		border-bottom: solid 30px #0c7404;
		border-left: solid 30px #0c7404;
		border-bottom-left-radius: 15px;
	}

	.macos-terminal-button-green-icon-1 {
		top: -143.5%;
		left: -156.5%;
		transform: scale(0.1);
	}

	.macos-terminal-button-green-icon-2 {
		top: -156.5%;
		left: -143.5%;
		transform: scale(0.1) rotate(180deg);
	}

	.macos-terminal-corner-anchor {
		position: absolute;
		border-right: solid 30px transparent;
		border-bottom: solid 30px transparent;
		border-top: solid 30px transparent;

		::before,
		::after {
			box-sizing: border-box;
		}
	}

	.macos-terminal-corner-anchor-top-left {
		left: -13px;
		top: -14.5px;
		cursor: nwse-resize;
		transform: scale(0.5) rotate(45deg);
	}

	.macos-terminal-corner-anchor-top-right {
		right: -13px;
		top: -14.5px;
		cursor: nesw-resize;
		transform: scale(0.5) rotate(135deg);
	}

	.macos-terminal-corner-anchor-bottom-left {
		left: -13px;
		bottom: -14px;
		cursor: nesw-resize;
		transform: scale(0.5) rotate(-45deg);
	}

	.macos-terminal-corner-anchor-bottom-right {
		right: -13px;
		bottom: -14px;
		cursor: nwse-resize;
		transform: scale(0.5) rotate(-135deg);
	}

	.macos-terminal-side-anchor {
		position: absolute;
	}

	.macos-terminal-side-anchor-top {
		top: 11.5px;
		width: 100%;
		height: 5px;
		cursor: n-resize;
	}

	.macos-terminal-side-anchor-left {
		bottom: 11.5px;
		top: 13px;
		left: -2.5px;
		width: 5px;
		height: 78%;
		cursor: w-resize;
	}

	.macos-terminal-side-anchor-bottom {
		bottom: 11.5px;
		width: 100%;
		height: 5px;
		cursor: s-resize;
	}

	.macos-terminal-side-anchor-right {
		bottom: 11.5px;
		top: 13px;
		right: -3px;
		width: 5px;
		height: 78%;
		cursor: e-resize;
	}
`;

function MacTerminalButtons(props) {
	const [isHovered, setHovered] = useState(false);

	return (
		<>
			<div
				className="macos-terminal-button red"
				onMouseEnter={() => setHovered(true)}
				onMouseOver={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
				onTouchStart={() => setHovered(true)}
				onTouchEnd={() => setHovered(false)}
				onTouchCancel={() => setHovered(false)}
			>
				{isHovered && (
					<>
						<div className="macos-terminal-button-red-icon macos-terminal-button-red-icon-1" />
						<div className="macos-terminal-button-red-icon macos-terminal-button-red-icon-2" />
					</>
				)}
			</div>
			<div
				className="macos-terminal-button yellow"
				onMouseEnter={() => setHovered(true)}
				onMouseOver={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
				onTouchStart={() => setHovered(true)}
				onTouchEnd={() => setHovered(false)}
				onTouchCancel={() => setHovered(false)}
			>
				{isHovered && (
					<>
						<div className="macos-terminal-button-yellow-icon" />
					</>
				)}
			</div>
			<div
				className="macos-terminal-button green"
				onMouseEnter={() => setHovered(true)}
				onMouseOver={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
				onTouchStart={() => setHovered(true)}
				onTouchEnd={() => setHovered(false)}
				onTouchCancel={() => setHovered(false)}
			>
				{isHovered && (
					<>
						<div className="macos-terminal-button-green-icon macos-terminal-button-green-icon-1" />
						<div className="macos-terminal-button-green-icon macos-terminal-button-green-icon-2" />
					</>
				)}
			</div>

			<div className="macos-terminal-side-anchor macos-terminal-side-anchor-top" />
			<div className="macos-terminal-side-anchor macos-terminal-side-anchor-left" />
			<div className="macos-terminal-side-anchor macos-terminal-side-anchor-right" />
			<div className="macos-terminal-side-anchor macos-terminal-side-anchor-bottom" />
			<div className="macos-terminal-corner-anchor macos-terminal-corner-anchor-top-left" />
			<div className="macos-terminal-corner-anchor macos-terminal-corner-anchor-top-right" />
			<div className="macos-terminal-corner-anchor macos-terminal-corner-anchor-bottom-left" />
			<div className="macos-terminal-corner-anchor macos-terminal-corner-anchor-bottom-right" />
		</>
	);
}

const SelfTypingText = styled(({ isMobile, ...props }) => <code {...props} />)`
	display: inline;
	text-align: center;
	font-size: ${({ isMobile }) => (isMobile ? "2.75vw" : "2vw")};
	cursor: type;
	line-height: 1;
	word-wrap: break-word;

	span {
		color: #76ff03 !important;
		text-shadow: #76ff03 0 0 5px !important;
	}

	span.Typewriter__wrapper::selection {
		color: #ffeb3b !important;
		-webkit-text-fill-color: #ffeb3b !important;
		background-color: #76ff03 !important;
	}

	span.Typewriter__wrapper::-moz-selection {
		color: #ffeb3b !important;
		-webkit-text-fill-color: #ffeb3b !important;
		background-color: #76ff03 !important;
	}

	span.Typewriter__cursor {
		-webkit-touch-callout: none !important;
		-webkit-user-select: none !important;
		-khtml-user-select: none !important;
		-moz-user-select: none !important;
		-ms-user-select: none !important;
		user-select: none !important;
	}

	.Typewriter {
		display: inline-block;
		border-radius: ${({ isMobile }) => (isMobile ? "1.8em" : "0.8em")};
		padding: 20px;
	}
`;

function ConsoleLogText(props) {
	const typewriterOnInit =
		(isMobile, ...consoleLogLines) =>
		(typewriter) => {
			typewriter.pauseFor(1000);
			consoleLogLines.forEach((line, i, arr) => {
				if (i > 0) {
					typewriter.deleteChars(arr[i - 1].length + CONSOLE_LOG_SUFFIX.length);
				}
				typewriter.pauseFor(250);
				if (i === 0) {
					typewriter.typeString(isMobile ? CONSOLE_LOG_PREFIX_MOBILE : CONSOLE_LOG_PREFIX);
				}
				typewriter.typeString(line + CONSOLE_LOG_SUFFIX).callFunction(() => {
					if (!document[`isPrintedConsoleLogLine${i + 1}`]) {
						console.log(line);
						document[`isPrintedConsoleLogLine${i + 1}`] = true;
					}
				});
				if (i < arr.length - 1) {
					typewriter.pauseFor(1500);
				}
			});
			typewriter.pauseFor(3000);
			for (let i = 0; i < 5; i++) {
				typewriter.deleteChars(1);
				typewriter.pauseFor(50);
			}
			typewriter.pauseFor(200);
			typewriter.start();
		};

	const typewriter = (
		<Typewriter
			options={{ delay: 50, deleteSpeed: 25, loop: true }}
			onInit={typewriterOnInit(
				false,
				"Welcome to my eportfolio",
				"Learn more about me here!",
				"And have a nice day~"
			)}
		/>
	);

	return (
		<div
			style={{
				cursor: "default",
				textAlign: "center",
				borderRadius: "5px",
				boxShadow: "0 0 15px #455a64",
				background: "linear-gradient(to right, #4f355f, #493b52, #424242, #373737)",
			}}
		>
			<MacTerminal>
				<MacTerminalButtons />
			</MacTerminal>
			<Responsive as={SelfTypingText} minWidth={responsiveMinWidth}>
				{typewriter}
			</Responsive>
			<Responsive as={SelfTypingText} maxWidth={responsiveMaxWidth} isMobile>
				{typewriter}
			</Responsive>
		</div>
	);
}

const memoComponent = memo(ConsoleLogText, (prev, next) => true);
export default memoComponent;
export { memoComponent as ConsoleLogText };
