import { memo, useState } from "react";
import { useDispatch } from "react-redux";
import Lazyload from "react-lazyload";
import { useSwipeable } from "react-swipeable";
import styled, { keyframes } from "styled-components";
import { NoSelect } from "../Styles";
import { LoadingIndicator } from "./LoadingIndicator";
import { changeImagePopupSource } from "../../redux/action";

const scrollImageHorizontally = (hiddenWidth) => keyframes`
	98% {
		transform: translateX(-${hiddenWidth}px);
	}
	100% {
		transform: translateX(0px);
	}
`;

const scrollImageVertically = (hiddenHeight) => keyframes`
	 98% {
	 	transform: translateY(-${hiddenHeight}px);
	 }
	 100% {
	 	transform: translateY(0px);
	 }
`;

const GalleryWrapper = styled.div`
	display: inline-block;
	position: relative;
	max-width: ${({ maxWidth }) => maxWidth};
	max-height: ${({ maxHeight }) => maxHeight};
	margin: 15px;
	border-radius: 20px;
	overflow: hidden;
	transition: 250ms;
	cursor: zoom-in;

	* {
		transition: 250ms;
	}

	img {
		object-fit: cover;
		animation: ${({ hiddenWidth, hiddenHeight }) =>
				hiddenWidth
					? scrollImageHorizontally(hiddenWidth)
					: hiddenHeight
					? scrollImageVertically(hiddenHeight)
					: "none"}
			15s linear infinite;
		${({ loaded }) => (loaded ? "" : "animation: loading-image 2.5s linear infinite;")};
	}

	@keyframes loading-image {
		50% {
			opacity: 0.5;
			filter: brightness(200%);
		}
	}
`;

const GalleryButtons = styled.div`
	.image-button {
		position: absolute;
		top: 0;
		width: 33%;
		height: 100%;
		border: none;
		transition: 500ms;
		background-color: transparent;
		cursor: ${({ loaded }) => (loaded ? "pointer" : "wait")};
		opacity: ${({ loaded }) => (loaded ? 0.75 : 0.25)};
	}

	.previous-image-button {
		left: 0;
		border-radius: 20px 0 0 20px;
	}

	.next-image-button {
		right: 0;
		border-radius: 0 20px 20px 0;
	}
`;

const IndexAndCount = styled.span`
	position: absolute;
	top: 10px;
	right: 10px;
	padding: 3px 10px 3px 10px;
	color: white;
	background-color: rgba(0, 0, 0, 0.8);
	border-radius: 15px;
	text-align: center;
`;

function Gallery(props) {
	const { images } = props;
	let { width, height } = props;
	if (!width && !height) {
		width = "300px";
		height = "300px";
	}
	width = (width || "auto") + "";
	height = (height || "auto") + "";

	const [displayIndex, setDisplayIndex] = useState(null);
	const [activeIndex, setActiveIndex] = useState(0);
	const [isLoaded, setLoaded] = useState(false);
	const [queryString, setQueryString] = useState("");
	const [preferredWidth, setPreferredWidth] = useState(width);
	const [hiddenWidth, setHiddenWidth] = useState(0);
	const [hiddenHeight, setHiddenHeight] = useState(0);
	const [originalWidth, setOriginalWidth] = useState(undefined);

	const dispatch = useDispatch();

	const swipeConfig = {
		onSwipedLeft: () => {
			if (!images || images.length < 2 || !isLoaded) {
				return;
			}
			setLoaded(false);
			setQueryString("");
			setActiveIndex((activeIndex + 1) % images.length);
		},
		onSwipedRight: () => {
			if (!images || images.length < 2 || !isLoaded) {
				return;
			}
			setLoaded(false);
			setQueryString("");
			setActiveIndex((activeIndex + images.length - 1) % images.length);
		},
		preventDefaultTouchmoveEvent: true,
	};
	const imageCenterSwipeHandlers = useSwipeable(swipeConfig);
	const buttonsSwipeHandlers = useSwipeable(swipeConfig);

	const GalleryButton = (props) => {
		const { className, clickToIndex } = props;

		const [isHovered, setHovered] = useState(false);

		return (
			<button
				className={className}
				onMouseEnter={() => setHovered(true)}
				onMouseOver={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
				onTouchStart={() => setHovered(true)}
				onTouchEnd={() => setHovered(false)}
				onTouchCancel={() => setHovered(false)}
				onClick={(e) => {
					e.preventDefault();
					e.stopPropagation();

					setHovered(false);

					if (!isLoaded) {
						return;
					}
					setLoaded(false);
					setQueryString("");
					setActiveIndex(clickToIndex);
				}}
				style={{ ...(isHovered && { backgroundColor: "rgba(3, 169, 244, 0.25)" }) }}
			/>
		);
	};

	return (
		<>
			{images && images.length > 0 && (
				<Lazyload once height={height} offset={400}>
					<GalleryWrapper
						loaded={isLoaded}
						maxWidth={width}
						maxHeight={height}
						hiddenWidth={hiddenWidth}
						hiddenHeight={hiddenHeight}
						onClick={() =>
							dispatch(
								changeImagePopupSource({
									images,
									activeIndex: activeIndex,
								})
							)
						}
						{...imageCenterSwipeHandlers}
					>
						<picture>
							<source
								type="image/webp"
								srcSet={encodeURIComponent(
									images[activeIndex].replace(/\.(jpg|png|gif)/, "-crop.webp")
								)}
							/>
							<img
								src={`${images[activeIndex]}${queryString}`}
								alt={images[activeIndex].replace(/\.(jpg|png|gif)/, "")}
								draggable={false}
								width={preferredWidth}
								onContextMenu={(e) => e.preventDefault()}
								onLoad={({ target }) => {
									setLoaded(true);
									setDisplayIndex(activeIndex);

									let windowWidth = originalWidth;
									const windowHeight = Number(height.replace(/px/, ""));
									const imgWidth = target.width;
									const imgHeight = target.height;
									let hiddenWidth = 0;
									let hiddenHeight = 0;

									if (!windowWidth) {
										setOriginalWidth(imgWidth);
										windowWidth = imgWidth;
									}

									if (imgWidth / imgHeight > windowWidth / windowHeight) {
										const newScaleWidth = (windowHeight * imgWidth) / imgHeight / windowWidth;
										setPreferredWidth(newScaleWidth * 100 + "%");

										hiddenWidth = (windowHeight * imgWidth) / imgHeight - windowWidth;
									}
									if (imgHeight / imgWidth > windowHeight / windowWidth) {
										setPreferredWidth("100%");
										hiddenHeight = (windowWidth * imgHeight) / imgWidth - windowHeight;
									}

									setHiddenWidth(hiddenWidth);

									if (imgWidth < windowWidth) {
										// const newImg = new Image();
										// newImg.onload = ({ target }) => {
										// 	setHiddenHeight(target.height - windowHeight);
										// };
										// newImg.src = target.src;
										setHiddenHeight(0);
									} else {
										setHiddenHeight(hiddenHeight);
									}
								}}
								onError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}
							/>
						</picture>
					</GalleryWrapper>

					{images.length >= 2 && (
						<GalleryButtons loaded={isLoaded} {...buttonsSwipeHandlers}>
							<GalleryButton
								className="image-button previous-image-button"
								clickToIndex={(activeIndex + images.length - 1) % images.length}
							/>
							<GalleryButton
								className="image-button next-image-button"
								clickToIndex={(activeIndex + 1) % images.length}
							/>
						</GalleryButtons>
					)}

					{images.length > 1 && (
						<NoSelect style={{ pointerEvents: "none" }}>
							<IndexAndCount>
								{displayIndex + 1}/{images.length}
							</IndexAndCount>
						</NoSelect>
					)}

					{!isLoaded && <LoadingIndicator />}
				</Lazyload>
			)}
		</>
	);
}

const memoComponent = memo(Gallery, (prev, next) => true);
export default memoComponent;
export { memoComponent as Gallery };
