export default "import { memo, useState, useEffect } from \"react\";\r\nimport { useSelector, useDispatch } from \"react-redux\";\r\nimport { useSwipeable } from \"react-swipeable\";\r\nimport styled from \"styled-components\";\r\nimport { LoadingIndicator } from \"./LoadingIndicator\";\r\nimport { NoSelect } from \"../Styles\";\r\nimport { changeImagePopupSource } from \"../../redux/action\";\r\n\r\nconst FullScreenGalleryWrapper = styled.div`\r\n\tdisplay: flex;\r\n\ttext-align: center;\r\n\tjustify-content: center;\r\n\talign-items: center;\r\n\r\n\tposition: fixed;\r\n\ttop: 0;\r\n\tleft: 0;\r\n\twidth: 100vw;\r\n\theight: 100vh;\r\n\tz-index: 1000;\r\n\tcursor: zoom-out;\r\n\ttransition: 500ms;\r\n\tbackground-color: rgba(0, 0, 0, 0.9);\r\n\topacity: ${({ show }) => (show ? 1 : 0)};\r\n\t${({ show }) => (show ? \"\" : \"pointer-events: none;\")};\r\n\r\n\timg {\r\n\t\twidth: 95vw;\r\n\t\theight: 95vh;\r\n\t\tobject-fit: contain;\r\n\t\ttransition: 500ms;\r\n\t\t${({ loaded }) => (loaded ? \"\" : \"filter: brightness(0.5);\")};\r\n\t}\r\n`;\r\n\r\nconst GalleryButtons = styled.div`\r\n\t.image-button {\r\n\t\tposition: fixed;\r\n\t\ttop: 0;\r\n\t\twidth: 33%;\r\n\t\theight: 100%;\r\n\t\tborder: none;\r\n\t\ttransition: 250ms;\r\n\t\tcursor: ${({ loaded }) => (loaded ? \"pointer\" : \"wait\")};\r\n\t\tbackground-color: transparent;\r\n\t}\r\n\r\n\t.previous-image-button {\r\n\t\tleft: 0;\r\n\t}\r\n\r\n\t.next-image-button {\r\n\t\tright: 0;\r\n\t}\r\n`;\r\n\r\nconst IndexAndCount = styled.span`\r\n\tposition: fixed;\r\n\ttop: 20px;\r\n\tright: 20px;\r\n\tpadding: 8px 15px 8px 15px;\r\n\tcolor: white;\r\n\tbackground-color: #455a64;\r\n\tborder-radius: 15px;\r\n\ttext-align: center;\r\n\tfont-size: 18px;\r\n\tbox-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.5);\r\n`;\r\n\r\nfunction FullScreenGallery(props) {\r\n\tconst { images, activeIndex, show, key } = useSelector((state) => state.imagePopupReducer);\r\n\tconst dispatch = useDispatch();\r\n\r\n\tconst [isLoaded, setLoaded] = useState(false);\r\n\tconst [queryString, setQueryString] = useState(\"\");\r\n\r\n\tuseEffect(() => {\r\n\t\tif (!show) {\r\n\t\t\tsetLoaded(false);\r\n\t\t}\r\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\r\n\t}, [show]);\r\n\r\n\tconst hasImage = images && images.length > 0;\r\n\tconst hidden = !hasImage || !show;\r\n\r\n\tconst swipeConfig = {\r\n\t\tonSwipedLeft: () => {\r\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\r\n\t\t\t\treturn;\r\n\t\t\t}\r\n\t\t\tsetLoaded(false);\r\n\t\t\tsetQueryString(\"\");\r\n\t\t\tdispatch(\r\n\t\t\t\tchangeImagePopupSource({\r\n\t\t\t\t\tactiveIndex: (activeIndex + 1) % images.length,\r\n\t\t\t\t})\r\n\t\t\t);\r\n\t\t},\r\n\t\tonSwipedRight: () => {\r\n\t\t\tif (!images || images.length < 2 || !isLoaded) {\r\n\t\t\t\treturn;\r\n\t\t\t}\r\n\t\t\tsetLoaded(false);\r\n\t\t\tsetQueryString(\"\");\r\n\t\t\tdispatch(\r\n\t\t\t\tchangeImagePopupSource({\r\n\t\t\t\t\tactiveIndex: (activeIndex + images.length - 1) % images.length,\r\n\t\t\t\t})\r\n\t\t\t);\r\n\t\t},\r\n\t\tonSwipedUp: () => {},\r\n\t\tonSwipedDown: () => {},\r\n\t\tpreventDefaultTouchmoveEvent: true,\r\n\t};\r\n\tconst imageCenterSwipeHandlers = useSwipeable(swipeConfig);\r\n\tconst buttonsSwipeHandlers = useSwipeable(swipeConfig);\r\n\r\n\tconst GalleryButton = (props) => {\r\n\t\tconst { className, clickToIndex } = props;\r\n\r\n\t\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\t\treturn (\r\n\t\t\t<button\r\n\t\t\t\tclassName={`image-button ${className}`}\r\n\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\tonClick={(e) => {\r\n\t\t\t\t\te.preventDefault();\r\n\t\t\t\t\te.stopPropagation();\r\n\r\n\t\t\t\t\tsetHovered(false);\r\n\r\n\t\t\t\t\tif (!isLoaded) {\r\n\t\t\t\t\t\treturn;\r\n\t\t\t\t\t}\r\n\r\n\t\t\t\t\tsetLoaded(false);\r\n\t\t\t\t\tsetQueryString(\"\");\r\n\t\t\t\t\tdispatch(changeImagePopupSource({ activeIndex: clickToIndex }));\r\n\t\t\t\t}}\r\n\t\t\t\tstyle={{ ...(isHovered && isLoaded && { backgroundColor: \"rgba(3, 169, 244, 0.25)\" }) }}\r\n\t\t\t/>\r\n\t\t);\r\n\t};\r\n\r\n\treturn (\r\n\t\t<>\r\n\t\t\t{show && (\r\n\t\t\t\t<FullScreenGalleryWrapper\r\n\t\t\t\t\tshow={!hidden}\r\n\t\t\t\t\tloaded={isLoaded}\r\n\t\t\t\t\tonClick={() => dispatch(changeImagePopupSource({ show: false }))}\r\n\t\t\t\t\t{...imageCenterSwipeHandlers}\r\n\t\t\t\t>\r\n\t\t\t\t\t<picture>\r\n\t\t\t\t\t\t<source\r\n\t\t\t\t\t\t\ttype=\"image/webp\"\r\n\t\t\t\t\t\t\tsrcSet={\r\n\t\t\t\t\t\t\t\thasImage\r\n\t\t\t\t\t\t\t\t\t? encodeURIComponent(images[activeIndex].replace(/\\.(jpg|png|gif)/, \".webp\"))\r\n\t\t\t\t\t\t\t\t\t: null\r\n\t\t\t\t\t\t\t}\r\n\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t<img\r\n\t\t\t\t\t\t\tkey={key}\r\n\t\t\t\t\t\t\tsrc={hasImage ? `${images[activeIndex]}${queryString}` : null}\r\n\t\t\t\t\t\t\talt={hasImage ? images[activeIndex].replace(/\\.(jpg|png|gif)/, \"\") : null}\r\n\t\t\t\t\t\t\tdraggable={false}\r\n\t\t\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\r\n\t\t\t\t\t\t\tonLoad={() => setLoaded(true)}\r\n\t\t\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\r\n\t\t\t\t\t\t/>\r\n\t\t\t\t\t</picture>\r\n\r\n\t\t\t\t\t{hasImage && images.length >= 2 && (\r\n\t\t\t\t\t\t<GalleryButtons loaded={isLoaded} {...buttonsSwipeHandlers}>\r\n\t\t\t\t\t\t\t<GalleryButton\r\n\t\t\t\t\t\t\t\tclassName=\"previous-image-button\"\r\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + images.length - 1) % images.length}\r\n\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t\t<GalleryButton\r\n\t\t\t\t\t\t\t\tclassName=\"next-image-button\"\r\n\t\t\t\t\t\t\t\tclickToIndex={(activeIndex + 1) % images.length}\r\n\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t</GalleryButtons>\r\n\t\t\t\t\t)}\r\n\r\n\t\t\t\t\t{hasImage && images.length > 1 && (\r\n\t\t\t\t\t\t<NoSelect style={{ pointerEvents: \"none\" }}>\r\n\t\t\t\t\t\t\t<IndexAndCount>\r\n\t\t\t\t\t\t\t\t{activeIndex + 1}/{images.length}\r\n\t\t\t\t\t\t\t</IndexAndCount>\r\n\t\t\t\t\t\t</NoSelect>\r\n\t\t\t\t\t)}\r\n\r\n\t\t\t\t\t{!isLoaded && <LoadingIndicator />}\r\n\t\t\t\t</FullScreenGalleryWrapper>\r\n\t\t\t)}\r\n\t\t</>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(FullScreenGallery, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as FullScreenGallery };\r\n";