export default "import { useState } from \"react\";\r\nimport { useDispatch } from \"react-redux\";\r\nimport Lazyload from \"react-lazyload\";\r\nimport { changeImagePopupSource } from \"../../../../../redux/action\";\r\n\r\nfunction CustomMarkdownImage(props) {\r\n\tconst { src, style, ...rest } = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\tconst [queryString, setQueryString] = useState(\"\");\r\n\r\n\tconst dispatch = useDispatch();\r\n\r\n\treturn (\r\n\t\t<Lazyload once height=\"400px\" offset={400}>\r\n\t\t\t<picture>\r\n\t\t\t\t<source type=\"image/webp\" srcSet={encodeURIComponent(src.replace(/\\.(jpg|png|gif)/, \".webp\"))} />\r\n\t\t\t\t<img\r\n\t\t\t\t\t{...rest}\r\n\t\t\t\t\tsrc={`${src}${queryString}`}\r\n\t\t\t\t\talt={src.replace(/\\.(jpg|png|gif)/, \"\")}\r\n\t\t\t\t\tdraggable=\"false\"\r\n\t\t\t\t\tonContextMenu={(e) => e.preventDefault()}\r\n\t\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\t\tonClick={() =>\r\n\t\t\t\t\t\tdispatch(\r\n\t\t\t\t\t\t\tchangeImagePopupSource({\r\n\t\t\t\t\t\t\t\timages: [src],\r\n\t\t\t\t\t\t\t\tactiveIndex: 0,\r\n\t\t\t\t\t\t\t})\r\n\t\t\t\t\t\t)\r\n\t\t\t\t\t}\r\n\t\t\t\t\tonError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}\r\n\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t...style,\r\n\t\t\t\t\t\t...(isHovered && {\r\n\t\t\t\t\t\t\ttransform: \"scale(1.2)\",\r\n\t\t\t\t\t\t\tboxShadow: \"0 0 30px rgba(0, 0, 0, 1)\",\r\n\t\t\t\t\t\t\tzIndex: 1,\r\n\t\t\t\t\t\t}),\r\n\t\t\t\t\t}}\r\n\t\t\t\t/>\r\n\t\t\t</picture>\r\n\t\t</Lazyload>\r\n\t);\r\n}\r\n\r\nfunction CustomImageRenderer(props) {\r\n\treturn <CustomMarkdownImage {...props} />;\r\n}\r\nexport default CustomImageRenderer;\r\nexport { CustomImageRenderer };\r\n";