export default "import styled from \"styled-components\";\r\n\r\nconst MarkdownLink = styled(({ hovered, ...props }) => <span {...props} />)`\r\n\ta {\r\n\t\tcolor: ${({ hovered }) => (hovered ? \"#3f51b5\" : \"#0288d1\")} !important;\r\n\t\ttransition: 250ms;\r\n\t\tposition: relative;\r\n\t\tfont-weight: bold;\r\n\r\n\t\t&::after {\r\n\t\t\tcontent: \"\";\r\n\t\t\tposition: absolute;\r\n\t\t\tbottom: 0;\r\n\t\t\tleft: 0;\r\n\t\t\twidth: ${({ hovered }) => (hovered ? 100 : 0)}%;\r\n\t\t\theight: 1px;\r\n\t\t\tbackground: #3f51b5;\r\n\t\t\ttransition: 250ms;\r\n\t\t}\r\n\t}\r\n`;\r\n\r\nconst exports = { MarkdownLink };\r\nexport default exports;\r\nexport { MarkdownLink };\r\n";