import { memo } from "react";
import "./index.css";

function ReadingProgressBar(props) {
	const { progress } = props;

	return <progress id="reading-progress-bar" max="100" value={progress * 100}></progress>;
}

const memoComponent = memo(ReadingProgressBar, (prev, next) => prev.progress === next.progress);
export default memoComponent;
export { memoComponent as ReadingProgressBar };
