function MarkdownContent(props) {
	const { markdown, fontSizeLevel } = props;

	const processedMarkdown = markdown
		? markdown.replace(/\t/g, "    ").replace(/^<!-- [\w-]*prettier[\w-]* -->$/gm, "")
		: null;

	const tocArray = markdown
		? MarkdownTocUtils.generateTocJsonFromMarkdown(processedMarkdown).map((toc) => ({
				...toc,
				content: toc.content.replace(/`/g, ""),
		  }))
		: [];

	const headingRenderer = ({ level, children }) => (
		<CustomHeadingRenderer tocArray={tocArray} level={level}>
			{children}
		</CustomHeadingRenderer>
	);

	return markdown ? (
		<div id="post-content">
			<CustomMarkdown
				children={processedMarkdown}
				remarkPlugins={[gfm]}
				fontSizeLevel={fontSizeLevel}
				components={{
					p: CustomParagraphRenderer,
					h1: headingRenderer,
					h2: headingRenderer,
					h3: headingRenderer,
					h4: headingRenderer,
					h5: headingRenderer,
					h6: headingRenderer,
					a: CustomLinkRenderer,
					table: CustomTableRenderer,
					img: CustomImageRenderer,
					// code: CustomCodeRenderer,
				}}
			/>
		</div>
	) : (
		<Header as="h2">Content not available.</Header>
	);
}
const memoComponent = memo(
	MarkdownContent,
	(prev, next) => prev.markdown === next.markdown && prev.fontSizeLevel === next.fontSizeLevel
);
export default memoComponent;
export { memoComponent as MarkdownContent };
