export default "import styled from \"styled-components\";\r\nimport { Button } from \"semantic-ui-react\";\r\n\r\nconst ScrollToTopButton = styled(({ navMenuOpen, ...props }) => <Button {...props} />)`\r\n\tbottom: 75px;\r\n\tright: 20px;\r\n\tposition: fixed;\r\n\tz-index: 1001;\r\n\ttransition: all 250ms, right 0ms !important;\r\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;\r\n\r\n\t@media (hover: hover) {\r\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\r\n\t}\r\n`;\r\n\r\nconst NavMenuButtonShadow = styled(({ navMenuOpen, ...props }) => <div {...props} />)`\r\n\twidth: 40px;\r\n\theight: 40px;\r\n\tbottom: 20px;\r\n\tright: 20px;\r\n\tposition: fixed;\r\n\tmargin-right: 4.5px;\r\n\tz-index: 1001;\r\n\tborder-radius: 10px;\r\n\ttransition: all 250ms, right 0ms !important;\r\n\tbackground: linear-gradient(-45deg, #ffa63d, #ff3d77, #338aff, #3cf0c5);\r\n\tbackground-size: 600%;\r\n\tanimation: animated-button 16s linear infinite !important;\r\n\tfilter: blur(15px);\r\n\tpointer-events: none;\r\n\r\n\t@media (hover: hover) {\r\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"32px\" : \"20px\")};\r\n\t}\r\n`;\r\n\r\nconst ButtonsBackground = styled(({ show, navMenuOpen, ...props }) => <div {...props} />)`\r\n\tposition: fixed;\r\n\tdisplay: block;\r\n\tbottom: 0;\r\n\tright: 0;\r\n\twidth: 200px;\r\n\theight: 275px;\r\n\tz-index: 1000;\r\n\tpointer-events: none;\r\n\ttransition: 0ms !important;\r\n\tbackground: linear-gradient(\r\n\t\tto bottom right,\r\n\t\ttransparent,\r\n\t\ttransparent,\r\n\t\ttransparent,\r\n\t\trgba(0, 0, 0, 0.25),\r\n\t\trgba(0, 0, 0, 0.7)\r\n\t);\r\n\t${({ show }) => (show ? \"\" : \"opacity: 0;\")}\r\n\r\n\t@media (hover: hover) {\r\n\t\tright: ${({ navMenuOpen }) => (navMenuOpen ? \"0px\" : \"-12px\")};\r\n\t}\r\n`;\r\n\r\nconst exports = { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground };\r\nexport default exports;\r\nexport { ScrollToTopButton, NavMenuButtonShadow, ButtonsBackground };\r\n"