export default "import axios from \"axios\";\r\nimport { firebaseHost } from \"../config\";\r\n\r\nconst postComment = ({ firstName, lastName, phone, email, message, clientIp }) => {\r\n\tconst date = new Date();\r\n\r\n\treturn axios.post(`${firebaseHost}/comments.json`, {\r\n\t\t...(firstName && { firstName }),\r\n\t\t...(lastName && { lastName }),\r\n\t\t...(phone && { phone }),\r\n\t\t...(email && { email }),\r\n\t\t...(message && { message }),\r\n\t\t...(clientIp && { clientIp }),\r\n\t\tdate: date.getTime(),\r\n\t\tdateISO: date.toISOString(),\r\n\t});\r\n};\r\n\r\nconst exports = { postComment };\r\nexport default exports;\r\nexport { postComment };\r\n";