import { memo } from "react";
import styled from "styled-components";
import { MenuItem } from "./MenuItem";

const PATHS = [
	{
		path: "/",
		title: "Home",
	},
	{
		path: "/workExp",
		title: "Work Experiences",
	},
	{
		path: "/hobbyProject",
		title: "Hobby Projects",
	},
	{
		path: "/personality",
		title: "Personality",
	},
	{
		path: "/blog",
		title: "Blog",
	},
	{
		path: "/contact",
		title: "Contact",
	},
];

const Overlay = styled(({ navMenuOpen, ...props }) => <div {...props} />)`
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: 250ms;
	overflow: hidden;
	z-index: 1000;

	opacity: ${({ navMenuOpen }) => (navMenuOpen ? 1 : 0)};
	visibility: ${({ navMenuOpen }) => (navMenuOpen ? "visible" : "hidden")};

	li {
		animation: ${({ navMenuOpen }) => (navMenuOpen ? "fadeInRight 0.5s ease forwards" : "")};
		animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.1s" : "")};

		&:nth-of-type(2) {
			animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.2s" : "")};
		}
		&:nth-of-type(3) {
			animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.3s" : "")};
		}
		&:nth-of-type(4) {
			animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.4s" : "")};
		}
		&:nth-of-type(5) {
			animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.5s" : "")};
		}
		&:nth-of-type(6) {
			animation-delay: ${({ navMenuOpen }) => (navMenuOpen ? "0.6s" : "")};
		}
	}

	nav {
		position: relative;
		height: 70%;
		top: 50%;
		transform: translateY(-50%);
		font-size: 30px;
		font-weight: bold;
		text-align: center;
	}

	ul {
		list-style: none;
		padding: 0;
		margin: 0 auto;
		display: inline-block;
		position: relative;
		height: 100%;

		li {
			display: block;
			height: 25%;
			height: calc(100% / ${PATHS.length});
			min-height: 50px;
			position: relative;
			opacity: 0;
		}
	}
`;

function FullScreenMenu(props) {
	const { navMenuOpen, menuItemCallback } = props;

	return (
		<Overlay navMenuOpen={navMenuOpen}>
			<nav>
				<ul>
					{PATHS.map((path, i) => (
						<li key={i}>
							<MenuItem
								title={path.title}
								path={path.path}
								menuItemCallback={menuItemCallback}
							></MenuItem>
						</li>
					))}
				</ul>
			</nav>
		</Overlay>
	);
}

const memoComponent = memo(FullScreenMenu, (prev, next) => prev.navMenuOpen === next.navMenuOpen);
export default memoComponent;
export { memoComponent as FullScreenMenu };
