export default "import { memo, useState } from \"react\";\r\nimport { Link } from \"react-router-dom\";\r\nimport { Grid, Label, Segment, Header, Icon, Responsive } from \"semantic-ui-react\";\r\nimport styled from \"styled-components\";\r\nimport { Gallery } from \"../reusable/Gallery\";\r\nimport { GradientButton } from \"../reusable/GradientButton\";\r\nimport { NoSelect, GradientText } from \"../Styles\";\r\n// import { ImageSlideshow } from \"../reusable/slideshow/ImageSlideshow\";\r\n// import { MobileImageSlideshow } from \"../reusable/slideshow/MobileImageSlideshow\";\r\nimport moment from \"moment\";\r\nimport { responsiveMaxWidth, responsiveMinWidth } from \"../../config\";\r\nimport isEmpty from \"lodash/isEmpty\";\r\n\r\nconst HeaderLinkWrapper = styled(({ hovered, ...props }) => <span {...props} />)`\r\n\ta {\r\n\t\ttransition: 250ms !important;\r\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"black\")} !important;\r\n\t}\r\n`;\r\n\r\nfunction Project(props) {\r\n\tconst { detail, inverted } = props;\r\n\tconst { path, projectName, projectNature, year, images, descriptionOverview, description } = detail;\r\n\r\n\tconst leftToRight = inverted !== true;\r\n\tconst colors = [\"#b71c1c\", \"#0d47a1\", \"#1b5e20\", \"#e65100\", \"#3e2723\"];\r\n\tconst yearColor = Number.isInteger(year) ? colors[(moment().year() % year) % colors.length] : \"#212121\";\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\tconst constructImage = () => (\r\n\t\t<Grid.Column width={8} textAlign=\"center\" verticalAlign={isEmpty(images) ? \"middle\" : null}>\r\n\t\t\t{(images || []).length > 0 ? (\r\n\t\t\t\t<div style={{ top: \"20px\", position: \"sticky\" }}>\r\n\t\t\t\t\t<Segment\r\n\t\t\t\t\t\traised\r\n\t\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t\tbackgroundColor: \"transparent\",\r\n\t\t\t\t\t\t\tborderRadius: \"1.5em\",\r\n\t\t\t\t\t\t\tpadding: 0,\r\n\t\t\t\t\t\t}}\r\n\t\t\t\t\t>\r\n\t\t\t\t\t\t<Gallery images={images} width=\"100%\" height=\"400px\" />\r\n\t\t\t\t\t</Segment>\r\n\t\t\t\t</div>\r\n\t\t\t) : (\r\n\t\t\t\t<Responsive as={NoSelect} minWidth={responsiveMinWidth}>\r\n\t\t\t\t\t<Label size=\"large\">\r\n\t\t\t\t\t\t<Icon name=\"dont\" />\r\n\t\t\t\t\t\tNo screenshot available\r\n\t\t\t\t\t</Label>\r\n\t\t\t\t</Responsive>\r\n\t\t\t)}\r\n\t\t</Grid.Column>\r\n\t);\r\n\r\n\tconst constructDescription = () => (\r\n\t\t<Grid.Column width={8}>\r\n\t\t\t<div style={{ top: \"20px\", position: \"sticky\" }}>\r\n\t\t\t\t<NoSelect>\r\n\t\t\t\t\t<Responsive\r\n\t\t\t\t\t\tas={Label}\r\n\t\t\t\t\t\tminWidth={responsiveMinWidth}\r\n\t\t\t\t\t\ttag\r\n\t\t\t\t\t\tsize=\"large\"\r\n\t\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t\tcolor: \"white\",\r\n\t\t\t\t\t\t\tbackgroundColor: yearColor,\r\n\t\t\t\t\t\t\tfloat: !leftToRight && \"right\",\r\n\t\t\t\t\t\t\ttransform: !leftToRight && \"scaleX(-1)\",\r\n\t\t\t\t\t\t}}\r\n\t\t\t\t\t>\r\n\t\t\t\t\t\t<div style={{ transform: !leftToRight && \"scaleX(-1)\" }}>\r\n\t\t\t\t\t\t\t{Number.isInteger(year) ? \"Year \" + year : year || \"Others\"}\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t</Responsive>\r\n\t\t\t\t\t<Responsive\r\n\t\t\t\t\t\tas={Label}\r\n\t\t\t\t\t\tmaxWidth={responsiveMaxWidth}\r\n\t\t\t\t\t\ttag\r\n\t\t\t\t\t\tsize=\"large\"\r\n\t\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t\tcolor: \"white\",\r\n\t\t\t\t\t\t\tbackgroundColor: yearColor,\r\n\t\t\t\t\t\t}}\r\n\t\t\t\t\t>\r\n\t\t\t\t\t\t{Number.isInteger(year) ? \"Year \" + year : year || \"Others\"}\r\n\t\t\t\t\t</Responsive>\r\n\t\t\t\t</NoSelect>\r\n\t\t\t\t<Header as=\"h2\" style={{ cursor: \"default\" }}>\r\n\t\t\t\t\t<HeaderLinkWrapper hovered={isHovered}>\r\n\t\t\t\t\t\t<Link\r\n\t\t\t\t\t\t\tclassName=\"allow-select\"\r\n\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\tstate={{ fromAllProjects: true }}\r\n\t\t\t\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t{projectName || \"\"}\r\n\t\t\t\t\t\t</Link>\r\n\t\t\t\t\t</HeaderLinkWrapper>\r\n\t\t\t\t\t<Header.Subheader>{projectNature || \"\"}</Header.Subheader>\r\n\t\t\t\t</Header>\r\n\t\t\t\t<GradientText>{description || descriptionOverview || \"\"}</GradientText>\r\n\r\n\t\t\t\t<div style={{ display: \"flex\", justifyContent: \"flex-end\", alignItems: \"center\", height: \"3em\" }}>\r\n\t\t\t\t\t<Link to={path} state={{ fromAllProjects: true }}>\r\n\t\t\t\t\t\t<Responsive as={GradientButton} minWidth={responsiveMinWidth} capitalize>\r\n\t\t\t\t\t\t\tLearn More\r\n\t\t\t\t\t\t\t<Icon name=\"chevron right\" size=\"large\" style={{ margin: \"0 -0.25em 0 0.25em\" }} />\r\n\t\t\t\t\t\t</Responsive>\r\n\t\t\t\t\t\t<Responsive as={GradientButton} maxWidth={responsiveMaxWidth} capitalize>\r\n\t\t\t\t\t\t\tDetails\r\n\t\t\t\t\t\t\t<Icon name=\"chevron right\" size=\"large\" style={{ margin: \"0 -0.25em 0 0.25em\" }} />\r\n\t\t\t\t\t\t</Responsive>\r\n\t\t\t\t\t</Link>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t</Grid.Column>\r\n\t);\r\n\r\n\treturn (\r\n\t\t<Grid stackable reversed={leftToRight && \"mobile\"} relaxed>\r\n\t\t\t{leftToRight ? (\r\n\t\t\t\t<>\r\n\t\t\t\t\t{constructImage()}\r\n\t\t\t\t\t{constructDescription()}\r\n\t\t\t\t</>\r\n\t\t\t) : (\r\n\t\t\t\t<>\r\n\t\t\t\t\t{constructDescription()}\r\n\t\t\t\t\t{constructImage()}\r\n\t\t\t\t</>\r\n\t\t\t)}\r\n\t\t</Grid>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(Project, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as Project };\r\n";