export default "import { useState } from \"react\";\r\nimport { Link } from \"react-router-dom\";\r\nimport { HashLink } from \"react-router-hash-link\";\r\nimport { MarkdownLink } from \"../Styles\";\r\nimport startsWith from \"lodash/startsWith\";\r\n\r\nfunction CustomMarkdownLink(props) {\r\n\tconst { href, children } = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t<MarkdownLink\r\n\t\t\thovered={isHovered}\r\n\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\tonClick={() => setHovered(false)}\r\n\t\t>\r\n\t\t\t{startsWith(href, \"#\") ? (\r\n\t\t\t\t<HashLink to={href} smooth replace className=\"allow-select\">\r\n\t\t\t\t\t{children}\r\n\t\t\t\t</HashLink>\r\n\t\t\t) : startsWith(href, \"http\") ? (\r\n\t\t\t\t<a href={href} target=\"_blank\" rel=\"external nofollow noopener noreferrer\" className=\"allow-select\">\r\n\t\t\t\t\t{children}\r\n\t\t\t\t</a>\r\n\t\t\t) : (\r\n\t\t\t\t<Link to={href} className=\"allow-select\">\r\n\t\t\t\t\t{children}\r\n\t\t\t\t</Link>\r\n\t\t\t)}\r\n\t\t</MarkdownLink>\r\n\t);\r\n}\r\n\r\nfunction CustomLinkRenderer(props) {\r\n\tconst { href, ...rest } = props;\r\n\r\n\treturn <CustomMarkdownLink href={decodeURIComponent(href)} {...rest} />;\r\n}\r\nexport default CustomLinkRenderer;\r\nexport { CustomLinkRenderer };\r\n";