export default "import { memo, Fragment } from \"react\";\r\nimport { Helmet } from \"react-helmet\";\r\nimport { Header, Divider } from \"semantic-ui-react\";\r\nimport { Body } from \"../../Styles\";\r\nimport { Project } from \"../Project\";\r\nimport { workProjects } from \"../../../config\";\r\n\r\nfunction WorkExp(props) {\r\n\treturn (\r\n\t\t<Body>\r\n\t\t\t<Helmet>\r\n\t\t\t\t<title>My Work Experiences | {process.env.REACT_APP_TITLE}</title>\r\n\t\t\t</Helmet>\r\n\r\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\r\n\t\t\t\tMy Work Experiences\r\n\t\t\t</Header>\r\n\r\n\t\t\t{(workProjects || [])\r\n\t\t\t\t.map(({ projectId, ...rest }) => ({ ...rest, path: `/workExp/${projectId}` }))\r\n\t\t\t\t.map((project, i) => (\r\n\t\t\t\t\t<Fragment key={i}>\r\n\t\t\t\t\t\t<Divider section />\r\n\t\t\t\t\t\t<Project detail={project} />\r\n\t\t\t\t\t</Fragment>\r\n\t\t\t\t))}\r\n\t\t</Body>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(WorkExp, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as WorkExp };\r\n";