export default "import { memo, useState } from \"react\";\r\nimport { Header } from \"semantic-ui-react\";\r\nimport { HashLink } from \"react-router-hash-link\";\r\nimport styled from \"styled-components\";\r\nimport toc from \"markdown-toc-unlazy\";\r\n\r\nconst CustomHeader = styled(({ hovered, ...props }) => <div {...props} />)`\r\n\ta {\r\n\t\tdisplay: block !important;\r\n\t\tfont-size: 1.25em !important;\r\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"#455a64\")} !important;\r\n\t\tbackground: ${({ hovered }) =>\r\n\t\t\thovered\r\n\t\t\t\t? \"linear-gradient(to right, rgba(244, 143, 177, 0.25), rgba(176, 190, 197, 0.25), rgba(176, 190, 197, 0.25))\"\r\n\t\t\t\t: \"rgba(176, 190, 197, 0.25)\"} !important;\r\n\t\ttransition: 250ms;\r\n\t}\r\n`;\r\n\r\nfunction HeaderWrapper(props) {\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t<CustomHeader\r\n\t\t\thovered={isHovered}\r\n\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\tonClick={() => setHovered(false)}\r\n\t\t>\r\n\t\t\t<Header {...props} />\r\n\t\t</CustomHeader>\r\n\t);\r\n}\r\n\r\nfunction TocMarkdown(props) {\r\n\tconst { markdown } = props;\r\n\r\n\tconst tableOfContents = markdown ? toc(markdown).json : null;\r\n\tconst minLevel = markdown ? Math.min(...tableOfContents.map((toc) => toc.lvl)) : null;\r\n\r\n\treturn (\r\n\t\t<>\r\n\t\t\t{markdown && (\r\n\t\t\t\t<div style={{ paddingBottom: \"40px\" }}>\r\n\t\t\t\t\t{tableOfContents.map((toc, i) => (\r\n\t\t\t\t\t\t<HeaderWrapper\r\n\t\t\t\t\t\t\tkey={i}\r\n\t\t\t\t\t\t\tas={HashLink}\r\n\t\t\t\t\t\t\tto={\"#\" + toc.slug}\r\n\t\t\t\t\t\t\tsmooth\r\n\t\t\t\t\t\t\treplace\r\n\t\t\t\t\t\t\tblock\r\n\t\t\t\t\t\t\tattached={i === 0 ? \"top\" : i === tableOfContents.length - 1 ? \"bottom\" : true}\r\n\t\t\t\t\t\t\tstyle={{ paddingLeft: (toc.lvl + 1 - minLevel) * 20 - 5 + \"px\" }}\r\n\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t{toc.content.replace(/`/g, \"\")}\r\n\t\t\t\t\t\t</HeaderWrapper>\r\n\t\t\t\t\t))}\r\n\t\t\t\t</div>\r\n\t\t\t)}\r\n\t\t</>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(TocMarkdown, (prev, next) => prev.markdown === next.markdown);\r\nexport default memoComponent;\r\nexport { memoComponent as TocMarkdown };\r\n";