export default "import { memo, useState } from \"react\";\r\nimport styled from \"styled-components\";\r\n\r\nconst ButtonWrapper = styled(({ hovered, capitalize, ...props }) => <div {...props} />)`\r\n\tbutton {\r\n\t\tdisplay: flex !important;\r\n\t\talign-items: center !important;\r\n\t\tjustify-content: center !important;\r\n\r\n\t\tpadding: 0.6em 1.25em !important;\r\n\t\tfont-size: 0.95em !important;\r\n\t\tfont-weight: bold !important;\r\n\t\t${({ capitalize }) => (capitalize ? \"text-transform: uppercase !important;\" : \"\")}\r\n\t\tcolor: white !important;\r\n\t\tcursor: pointer !important;\r\n\t\ttransition: 250ms ease-in-out !important;\r\n\t\tborder: none !important;\r\n\t\tborder-radius: 2em !important;\r\n\t\tbox-shadow: 0 0 1.5em ${({ hovered }) => (hovered ? \"#244ab6\" : \"#b63c18\")} !important;\r\n\t\tbackground-size: 300% 100% !important;\r\n\t\tbackground-image: linear-gradient(to right, #ff1744, #ff9100, #651fff, #00b0ff) !important;\r\n\t\t${({ hovered }) => (hovered ? \"background-position: 100% 0; !important\" : \"\")}\r\n\t}\r\n`;\r\n\r\nfunction GradientButton(props) {\r\n\tconst { children = \"\", capitalize, action, ...rest } = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t<ButtonWrapper capitalize={capitalize} hovered={isHovered}>\r\n\t\t\t<button\r\n\t\t\t\t{...rest}\r\n\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\tonClick={() => {\r\n\t\t\t\t\tsetHovered(false);\r\n\t\t\t\t\tif (typeof action === \"function\") {\r\n\t\t\t\t\t\taction();\r\n\t\t\t\t\t}\r\n\t\t\t\t}}\r\n\t\t\t>\r\n\t\t\t\t{children}\r\n\t\t\t</button>\r\n\t\t</ButtonWrapper>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(GradientButton, (prev, next) => prev.action === next.action);\r\nexport default memoComponent;\r\nexport { memoComponent as GradientButton };\r\n";