import { CHANGE_IMAGE_POPUP_SOURCE } from "../action/actionTypes";

const imagePopupReducer = (
	state = {
		images: [],
		activeIndex: null,
		show: false,
		key: null,
	},
	{ type, data }
) => {
	switch (type) {
		case CHANGE_IMAGE_POPUP_SOURCE:
			const hasImages = data.images && data.images.length > 0;
			return {
				...state,
				...data,
				show: (data.show === undefined && (data.activeIndex >= 0 || hasImages)) || data.show,
				key:
					hasImages || (typeof data.show === "boolean" && state.show !== data.show)
						? Math.random()
						: state.key,
			};
		default:
			return state;
	}
};

export default imagePopupReducer;
export { imagePopupReducer };
