export default "import { memo, useState } from \"react\";\r\nimport styled from \"styled-components\";\r\nimport { NoSelect } from \"../Styles\";\r\nimport { LocalStorageService } from \"../../service\";\r\n\r\nconst CookiesConsentWrapper = styled(({ consent, ...props }) => <div {...props} />)`\r\n\tdisplay: flex;\r\n\tjustify-content: center;\r\n\talign-items: center;\r\n\r\n\tposition: fixed;\r\n\tbottom: ${({ consent }) => (consent === 2 ? 0 : \"-100%\")};\r\n\twidth: 100%;\r\n\tpadding: 0.75em 1em;\r\n\tfont-size: 16px;\r\n\tcolor: #c1d5e0;\r\n\tbackground-color: rgba(38, 50, 56, 0.95);\r\n\tbox-shadow: 0 0 10px rgba(0, 0, 0, 0.5);\r\n\tz-index: 2000;\r\n\tcursor: default;\r\n\r\n\tanimation: ${({ consent }) => (consent === 2 ? \"close\" : \"expand\")} 1.5s forwards;\r\n\t${({ consent }) => (consent === 2 ? \"\" : \"animation-delay: 1s;\")}\r\n\r\n\t@keyframes expand {\r\n\t\t100% {\r\n\t\t\tbottom: 0;\r\n\t\t}\r\n\t}\r\n\r\n\t@keyframes close {\r\n\t\t100% {\r\n\t\t\tbottom: -100%;\r\n\t\t}\r\n\t}\r\n`;\r\n\r\nconst CookiesConsentButton = styled.div`\r\n\tdisplay: flex;\r\n\tjustify-content: center;\r\n\talign-items: center;\r\n\r\n\tpadding: 10px 15px;\r\n\tmargin-left: 1em;\r\n\tz-index: 2001;\r\n\tborder-radius: 5px;\r\n\tcursor: pointer;\r\n\tfont-size: 15px;\r\n\tfont-weight: bold;\r\n\tcolor: #f57f17;\r\n\tbackground-color: #fdd835;\r\n\twhite-space: nowrap;\r\n\ttransition: 250ms !important;\r\n\r\n\t&:hover {\r\n\t\tfilter: brightness(0.85);\r\n\t}\r\n`;\r\n\r\nfunction CookiesConsent(props) {\r\n\t// 1 means already accepted before\r\n\t// 2 means just accepted\r\n\t// else means not accepted yet\r\n\tconst [consent, setConsent] = useState(LocalStorageService.getCookiesConsent());\r\n\r\n\treturn (\r\n\t\t<>\r\n\t\t\t{consent !== 1 && (\r\n\t\t\t\t<NoSelect>\r\n\t\t\t\t\t<CookiesConsentWrapper consent={consent}>\r\n\t\t\t\t\t\tThis website uses cookies, local storage and/or similar technologies to store user settings so\r\n\t\t\t\t\t\tas to improve user experience.\r\n\t\t\t\t\t\t<CookiesConsentButton\r\n\t\t\t\t\t\t\tonClick={() => {\r\n\t\t\t\t\t\t\t\tsetConsent(2);\r\n\t\t\t\t\t\t\t\tLocalStorageService.setCookiesConsent(true);\r\n\t\t\t\t\t\t\t}}\r\n\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\tAccept\r\n\t\t\t\t\t\t</CookiesConsentButton>\r\n\t\t\t\t\t</CookiesConsentWrapper>\r\n\t\t\t\t</NoSelect>\r\n\t\t\t)}\r\n\t\t</>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(CookiesConsent, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as CookiesConsent };\r\n";