export default "import { memo, useEffect } from \"react\";\r\nimport { Routes, Route, useLocation, useNavigationType } from \"react-router-dom\";\r\nimport { useDispatch } from \"react-redux\";\r\n// import { useTransition animated } from \"react-spring\";\r\nimport { SiteHeader } from \"./header/SiteHeader\";\r\nimport { Home, WorkExp, HobbyProject, ProjectDetail, Personality, Blog, BlogPost, Contact } from \"./center/pages\";\r\nimport { SiteFooter } from \"./footer/SiteFooter\";\r\nimport { FullScreenGallery } from \"./reusable/FullScreenGallery\";\r\nimport { changeImagePopupSource } from \"../redux/action\";\r\n\r\nfunction Eportfolio(props) {\r\n\tconst { historyChangeCallback } = props;\r\n\r\n\t// const location = useLocation();\r\n\t// const transitions = useTransition(location, (location) => location.pathname, {\r\n\t// \tfrom: { opacity: 0, position: \"absolute\" },\r\n\t// \tenter: { opacity: 1 },\r\n\t// \tleave: { opacity: 0 },\r\n\t// });\r\n\r\n\tconst location = useLocation();\r\n\tconst navigationType = useNavigationType();\r\n\tconst dispatch = useDispatch();\r\n\r\n\tuseEffect(() => {\r\n\t\tdispatch(changeImagePopupSource({ show: false }));\r\n\r\n\t\tif (!location.hash && navigationType === \"PUSH\") {\r\n\t\t\twindow.scrollTo({ top: 0 });\r\n\t\t}\r\n\t\tif (historyChangeCallback) {\r\n\t\t\thistoryChangeCallback();\r\n\t\t}\r\n\t\t// eslint-disable-next-line react-hooks/exhaustive-deps\r\n\t}, [location]);\r\n\r\n\treturn (\r\n\t\t<>\r\n\t\t\t<SiteHeader />\r\n\r\n\t\t\t{/* {transitions.map(({ item, props, key }) => (\r\n\t\t\t\t<animated.div key={key} style={{ ...props, width: \"100%\" }}> */}\r\n\t\t\t<Routes caseSensitive /*location={item}*/>\r\n\t\t\t\t<Route path=\"/*\" element={<Home />} />\r\n\t\t\t\t<Route\r\n\t\t\t\t\tpath=\"/workExp/:projectId\"\r\n\t\t\t\t\telement={<ProjectDetail projectType=\"My Work Experiences\" backPath=\"/workExp\" />}\r\n\t\t\t\t/>\r\n\t\t\t\t<Route path=\"/workExp\" element={<WorkExp />} />\r\n\t\t\t\t<Route\r\n\t\t\t\t\tpath=\"/hobbyProject/:projectId\"\r\n\t\t\t\t\telement={<ProjectDetail projectType=\"My Hobby Projects\" backPath=\"/hobbyProject\" />}\r\n\t\t\t\t/>\r\n\t\t\t\t<Route path=\"/hobbyProject\" element={<HobbyProject />} />\r\n\t\t\t\t<Route path=\"/personality\" element={<Personality />} />\r\n\t\t\t\t<Route path=\"/blog/:postId\" element={<BlogPost />} />\r\n\t\t\t\t<Route path=\"/blog\" element={<Blog />} />\r\n\t\t\t\t<Route path=\"/contact\" element={<Contact />} />\r\n\t\t\t</Routes>\r\n\t\t\t<SiteFooter />\r\n\t\t\t<FullScreenGallery />\r\n\t\t\t{/* </animated.div>\r\n\t\t\t))} */}\r\n\t\t</>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(Eportfolio, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as Eportfolio };\r\n";