import { memo } from "react";
import { Navigate, useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import { Grid, Segment } from "semantic-ui-react";
import { Avatar } from "./Avatar";
import { CircleShortcut } from "./CircleShortcut";
import { Body } from "../../../Styles";
import { GradientText } from "../../../Styles";
import { ConsoleLogText } from "./ConsoleLogText";
import moment from "moment";

function Home(props) {
	const age = moment().diff("1994-11-06", "years");

	const location = useLocation();

	return location.pathname !== "/" ? (
		<Navigate to="/" replace />
	) : (
		<Body>
			<Helmet>
				<title>Home | {process.env.REACT_APP_TITLE}</title>
			</Helmet>

			<Grid stackable centered>
				<Grid.Column width={6} stretched>
					<Avatar></Avatar>
				</Grid.Column>

				<Grid.Column width={10} stretched>
					<Segment vertical basic>
						<ConsoleLogText />
					</Segment>

					<Segment vertical basic>
						<Grid stackable padded columns={3}>
							<Grid.Column textAlign="center">
								<CircleShortcut
									linkTo="/workExp"
									text="Work"
									themeColor="red"
									normalColor="#f44336"
									hoverColor="#ffcdd2"
									normalBg="#ffcdd2"
									hoverBg="#b71c1c"
									glowEffectBg="#f44336"
								/>
							</Grid.Column>
							<Grid.Column textAlign="center">
								<CircleShortcut
									linkTo="/hobbyProject"
									text="Hobby Projects"
									themeColor="blue"
									normalColor="#2196f3"
									hoverColor="#bbdefb"
									normalBg="#bbdefb"
									hoverBg="#0d47a1"
									glowEffectBg="#2196f3"
								/>
							</Grid.Column>
							<Grid.Column textAlign="center">
								<CircleShortcut
									linkTo="/blog"
									text="Blog"
									themeColor="green"
									normalColor="#4caf50"
									hoverColor="#c8e6c9"
									normalBg="#c8e6c9"
									hoverBg="#1b5e20"
									glowEffectBg="#4caf50"
								/>
							</Grid.Column>
						</Grid>
					</Segment>

					<Segment vertical>
						<GradientText>
							I am a {age}-year-old web developer who is enthusiastic about building software that
							requires logical thinking, problem solving, creativity and design elements.
							<br />
							<br />
							I find programming itself to be a great way to achieve things like automating repetitive
							work. In this digital age, I am proud of being able to think digitally and do things
							digitally and keep up with the fashion.
							<br />
							<br />I am seeking to work with a company where I can effectively utilize my versatile skill
							set and innovative ideas to contribute towards the company's growth.
						</GradientText>
					</Segment>
				</Grid.Column>
			</Grid>
		</Body>
	);
}

const memoComponent = memo(Home, (prev, next) => true);
export default memoComponent;
export { memoComponent as Home };
