import { memo, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import styled from "styled-components";

const MenuItemWrapper = styled(({ hovered, ...props }) => <span {...props} />)`
	a {
		position: relative;
		color: #e91e63;
		text-decoration: none;
		overflow: hidden;

		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		-webkit-user-drag: none;
		-khtml-user-drag: none;
		-moz-user-drag: none;
		-ms-user-drag: none;
		user-drag: none;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			width: ${({ hovered }) => (hovered ? 100 : 0)}%;
			transform: translateX(-50%);
			height: 3px;
			background: #e91e63;
			transition: 250ms;
		}
	}
`;

function MenuItem(props) {
	const { title, path, menuItemCallback } = props;

	const [isHovered, setHovered] = useState(false);

	const { pathname } = useLocation();

	return (
		<MenuItemWrapper hovered={isHovered}>
			<Link
				className="code"
				to={path}
				onMouseEnter={() => setHovered(true)}
				onMouseOver={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
				onTouchStart={() => setHovered(true)}
				onTouchEnd={() => setHovered(false)}
				onTouchCancel={() => setHovered(false)}
				onClick={() => {
					setHovered(false);
					if (pathname !== path && menuItemCallback) {
						menuItemCallback();
					}
				}}
				style={{
					...(pathname === path && {
						opacity: 0.25,
						pointerEvents: "none",
					}),
				}}
			>
				{title}
			</Link>
		</MenuItemWrapper>
	);
}

const memoComponent = memo(
	MenuItem,
	(prev, next) =>
		prev.title === next.title && prev.path === next.path && prev.menuItemCallback === next.menuItemCallback
);
export default memoComponent;
export { memoComponent as MenuItem };
