const isScrollToTopButtonVisible = () =>
	document.scrollingElement && document.scrollingElement.scrollTop >= document.scrollingElement.offsetHeight * 0.75;

const isScrolledToBottom = () =>
	Math.abs(
		document.scrollingElement.scrollTop +
			document.scrollingElement.offsetHeight -
			document.scrollingElement.scrollHeight
	) < 20;

const scrollToElement = (element, delay) =>
	setTimeout(() => {
		if (
			// element.getBoundingClientRect().top means how much window's top is above the element's top
			(isScrolledToBottom() && element.getBoundingClientRect().top < 0) ||
			(!isScrolledToBottom() && Math.abs(element.getBoundingClientRect().top) > 20)
		) {
			element.scrollIntoView(true);
			scrollToElement(element, 250);
		}
	}, delay);

const ScrollUtils = {
	isScrollToTopButtonVisible,
	isScrolledToBottom,
	scrollToElement,
};

export default ScrollUtils;
export { isScrollToTopButtonVisible, isScrolledToBottom, scrollToElement };
