export default "import { memo, useState } from \"react\";\r\nimport { Link } from \"react-router-dom\";\r\nimport { Helmet } from \"react-helmet\";\r\nimport {\r\n\tSegment,\r\n\tHeader,\r\n\tItem,\r\n\tCard,\r\n\tLabel,\r\n\tButton,\r\n\tIcon,\r\n\tImage,\r\n\tMessage,\r\n\tDivider,\r\n\tResponsive,\r\n} from \"semantic-ui-react\";\r\nimport styled from \"styled-components\";\r\nimport { Body, NoSelect } from \"../../../Styles\";\r\nimport { responsiveMaxWidth, responsiveMinWidth, blogPosts, blogPostTags } from \"../../../../config\";\r\nimport moment from \"moment\";\r\nimport get from \"lodash/get\";\r\nimport find from \"lodash/find\";\r\nimport union from \"lodash/union\";\r\nimport intersection from \"lodash/intersection\";\r\nimport without from \"lodash/without\";\r\nimport uniqBy from \"lodash/uniqBy\";\r\nimport { LocalStorageService } from \"../../../../service\";\r\n\r\nconst CustomItemGroup = styled(Item.Group)`\r\n\ta {\r\n\t\ttransition: 250ms !important;\r\n\r\n\t\t&:hover {\r\n\t\t\tcolor: #e91e63 !important;\r\n\t\t}\r\n\t}\r\n\r\n\timg {\r\n\t\tmax-width: 80px !important;\r\n\t\tmax-height: 80px !important;\r\n\t\twidth: auto !important;\r\n\t\theight: auto !important;\r\n\t\tmargin: auto !important;\r\n\t}\r\n`;\r\n\r\nconst CustomCardGroup = styled(Item.Group)`\r\n\timg {\r\n\t\tmax-width: 40px !important;\r\n\t\tmax-height: 40px !important;\r\n\t\twidth: auto !important;\r\n\t\theight: auto !important;\r\n\t\tfloat: right !important;\r\n\t}\r\n`;\r\n\r\nconst HeaderLink = styled(({ hovered, ...props }) => <span {...props} />)`\r\n\ta {\r\n\t\ttransition: 250ms !important;\r\n\t\tcolor: ${({ hovered }) => (hovered ? \"#e91e63\" : \"black\")} !important;\r\n\t}\r\n`;\r\n\r\nfunction HeaderLinkWrapper(props) {\r\n\tconst { title, path } = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t<HeaderLink hovered={isHovered}>\r\n\t\t\t<Link\r\n\t\t\t\tto={path}\r\n\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t>\r\n\t\t\t\t{title}\r\n\t\t\t</Link>\r\n\t\t</HeaderLink>\r\n\t);\r\n}\r\n\r\nfunction Blog(props) {\r\n\tconst readBlogPostIdsFromLocalStorage = LocalStorageService.getReadBlogPostIds();\r\n\r\n\tconst allBlogPostTags = uniqBy(\r\n\t\t[\r\n\t\t\t...blogPostTags,\r\n\t\t\t...blogPosts.flatMap(({ tags = [] }) => tags).map((name) => ({ name, icon: \"hashtag\", color: \"grey\" })),\r\n\t\t],\r\n\t\t\"name\"\r\n\t);\r\n\tconst [wantedTagNames, setWantedTagNames] = useState([]);\r\n\tconst [unwantedTagNames, setUnwantedTagNames] = useState([]);\r\n\r\n\tconst filteredBlogPosts = blogPosts\r\n\t\t.filter(\r\n\t\t\t({ tags = [] }) =>\r\n\t\t\t\t(wantedTagNames.length === 0 || intersection(tags, wantedTagNames).length > 0) &&\r\n\t\t\t\tintersection(tags, unwantedTagNames).length === 0\r\n\t\t)\r\n\t\t.slice()\r\n\t\t.reverse();\r\n\r\n\tconst toBlogTags = (tags) => (\r\n\t\t<>\r\n\t\t\t{tags.map((tag, i) => (\r\n\t\t\t\t<Label\r\n\t\t\t\t\tkey={i}\r\n\t\t\t\t\tas=\"a\"\r\n\t\t\t\t\tcolor={get(find(allBlogPostTags, { name: tag }), \"color\", \"grey\")}\r\n\t\t\t\t\tonClick={() => setWantedTagNames(union(wantedTagNames, [tag]))}\r\n\t\t\t\t>\r\n\t\t\t\t\t<Icon name={get(find(allBlogPostTags, { name: tag }), \"icon\", \"hashtag\")} />\r\n\t\t\t\t\t{tag}\r\n\t\t\t\t</Label>\r\n\t\t\t))}\r\n\t\t</>\r\n\t);\r\n\r\n\treturn (\r\n\t\t<Body>\r\n\t\t\t<Helmet>\r\n\t\t\t\t<title>All Blog Posts | {process.env.REACT_APP_TITLE}</title>\r\n\t\t\t</Helmet>\r\n\r\n\t\t\t<Header size=\"huge\" style={{ cursor: \"default\" }}>\r\n\t\t\t\tAll Blog Posts\r\n\t\t\t</Header>\r\n\t\t\t<Divider section />\r\n\r\n\t\t\t<Segment vertical basic clearing>\r\n\t\t\t\t<NoSelect>\r\n\t\t\t\t\t<Label.Group style={{ textAlign: \"center\" }}>\r\n\t\t\t\t\t\t{allBlogPostTags.map((tag, i) => (\r\n\t\t\t\t\t\t\t<Label\r\n\t\t\t\t\t\t\t\tkey={i}\r\n\t\t\t\t\t\t\t\tas=\"a\"\r\n\t\t\t\t\t\t\t\tcolor={tag.color || \"grey\"}\r\n\t\t\t\t\t\t\t\tstyle={{\r\n\t\t\t\t\t\t\t\t\topacity:\r\n\t\t\t\t\t\t\t\t\t\twantedTagNames.includes(tag.name) || unwantedTagNames.includes(tag.name)\r\n\t\t\t\t\t\t\t\t\t\t\t? 1\r\n\t\t\t\t\t\t\t\t\t\t\t: 0.35,\r\n\t\t\t\t\t\t\t\t}}\r\n\t\t\t\t\t\t\t\tonClick={() => {\r\n\t\t\t\t\t\t\t\t\tif (wantedTagNames.includes(tag.name)) {\r\n\t\t\t\t\t\t\t\t\t\tsetWantedTagNames(without(wantedTagNames, tag.name));\r\n\t\t\t\t\t\t\t\t\t\tsetUnwantedTagNames(union(unwantedTagNames, [tag.name]));\r\n\t\t\t\t\t\t\t\t\t} else if (unwantedTagNames.includes(tag.name)) {\r\n\t\t\t\t\t\t\t\t\t\tsetUnwantedTagNames(without(unwantedTagNames, tag.name));\r\n\t\t\t\t\t\t\t\t\t} else {\r\n\t\t\t\t\t\t\t\t\t\tsetWantedTagNames(union(wantedTagNames, [tag.name]));\r\n\t\t\t\t\t\t\t\t\t}\r\n\t\t\t\t\t\t\t\t}}\r\n\t\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t\t<Icon\r\n\t\t\t\t\t\t\t\t\tname={\r\n\t\t\t\t\t\t\t\t\t\t(wantedTagNames.includes(tag.name)\r\n\t\t\t\t\t\t\t\t\t\t\t? \"check\"\r\n\t\t\t\t\t\t\t\t\t\t\t: unwantedTagNames.includes(tag.name)\r\n\t\t\t\t\t\t\t\t\t\t\t? \"x\"\r\n\t\t\t\t\t\t\t\t\t\t\t: tag.icon) || \"hashtag\"\r\n\t\t\t\t\t\t\t\t\t}\r\n\t\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t\t\t{tag.name}\r\n\t\t\t\t\t\t\t</Label>\r\n\t\t\t\t\t\t))}\r\n\t\t\t\t\t</Label.Group>\r\n\t\t\t\t</NoSelect>\r\n\t\t\t</Segment>\r\n\r\n\t\t\t{filteredBlogPosts && filteredBlogPosts.length > 0 ? (\r\n\t\t\t\t<Segment vertical>\r\n\t\t\t\t\t<Responsive as={CustomItemGroup} minWidth={responsiveMinWidth} divided relaxed>\r\n\t\t\t\t\t\t{filteredBlogPosts.map(({ postId, path, thumbnail, title, date, description, tags }, i) => (\r\n\t\t\t\t\t\t\t<Item key={i}>\r\n\t\t\t\t\t\t\t\t<Item.Image\r\n\t\t\t\t\t\t\t\t\tas={Link}\r\n\t\t\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\t\t\t\t\t\tsrc={thumbnail}\r\n\t\t\t\t\t\t\t\t\tsize=\"tiny\"\r\n\t\t\t\t\t\t\t\t\tstyle={{ display: \"flex\", justifyContent: \"center\", alignItems: \"center\" }}\r\n\t\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t\t\t<Item.Content>\r\n\t\t\t\t\t\t\t\t\t<Item.Header\r\n\t\t\t\t\t\t\t\t\t\tas={Link}\r\n\t\t\t\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\t\t\t\t\t\t\tclassName=\"allow-select\"\r\n\t\t\t\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t\t\t\t{title}\r\n\t\t\t\t\t\t\t\t\t\t{!readBlogPostIdsFromLocalStorage.includes(postId) && (\r\n\t\t\t\t\t\t\t\t\t\t\t<Label color=\"pink\" circular empty style={{ marginLeft: \"5px\" }} />\r\n\t\t\t\t\t\t\t\t\t\t)}\r\n\t\t\t\t\t\t\t\t\t</Item.Header>\r\n\t\t\t\t\t\t\t\t\t<Item.Meta style={{ cursor: \"default\" }}>\r\n\t\t\t\t\t\t\t\t\t\t<span>{moment(date).format(\"YYYY-MM-DD\")}</span>\r\n\t\t\t\t\t\t\t\t\t</Item.Meta>\r\n\t\t\t\t\t\t\t\t\t<Item.Description style={{ cursor: \"default\" }}>{description}</Item.Description>\r\n\t\t\t\t\t\t\t\t\t<Item.Extra>\r\n\t\t\t\t\t\t\t\t\t\t<Button\r\n\t\t\t\t\t\t\t\t\t\t\tas={Link}\r\n\t\t\t\t\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\t\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\t\t\t\t\t\t\t\tbasic\r\n\t\t\t\t\t\t\t\t\t\t\tcompact\r\n\t\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\r\n\t\t\t\t\t\t\t\t\t\t\tcolor=\"pink\"\r\n\t\t\t\t\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t\t\t\t\tContinue reading\r\n\t\t\t\t\t\t\t\t\t\t\t<Icon name=\"right chevron\" />\r\n\t\t\t\t\t\t\t\t\t\t</Button>\r\n\t\t\t\t\t\t\t\t\t\t{tags && tags.length ? (\r\n\t\t\t\t\t\t\t\t\t\t\t<NoSelect>\r\n\t\t\t\t\t\t\t\t\t\t\t\t<Label.Group>{toBlogTags(tags)}</Label.Group>\r\n\t\t\t\t\t\t\t\t\t\t\t</NoSelect>\r\n\t\t\t\t\t\t\t\t\t\t) : null}\r\n\t\t\t\t\t\t\t\t\t</Item.Extra>\r\n\t\t\t\t\t\t\t\t</Item.Content>\r\n\t\t\t\t\t\t\t</Item>\r\n\t\t\t\t\t\t))}\r\n\t\t\t\t\t</Responsive>\r\n\r\n\t\t\t\t\t<Responsive\r\n\t\t\t\t\t\tas={CustomCardGroup}\r\n\t\t\t\t\t\tmaxWidth={responsiveMaxWidth}\r\n\t\t\t\t\t\tstyle={{ width: \"100%\", display: \"block\" }}\r\n\t\t\t\t\t>\r\n\t\t\t\t\t\t{filteredBlogPosts.map(({ postId, path, thumbnail, title, date, description, tags }, i) => (\r\n\t\t\t\t\t\t\t<Card\r\n\t\t\t\t\t\t\t\tkey={i}\r\n\t\t\t\t\t\t\t\tstyle={{ width: \"100%\", display: \"block\", background: \"rgba(255, 255, 255, 0.4)\" }}\r\n\t\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t\t<Card.Content>\r\n\t\t\t\t\t\t\t\t\t<Image\r\n\t\t\t\t\t\t\t\t\t\tas={Link}\r\n\t\t\t\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\r\n\t\t\t\t\t\t\t\t\t\tsrc={thumbnail}\r\n\t\t\t\t\t\t\t\t\t/>\r\n\t\t\t\t\t\t\t\t\t<Card.Header>\r\n\t\t\t\t\t\t\t\t\t\t<HeaderLinkWrapper title={title} path={path} />\r\n\t\t\t\t\t\t\t\t\t\t{!readBlogPostIdsFromLocalStorage.includes(postId) && (\r\n\t\t\t\t\t\t\t\t\t\t\t<Label color=\"pink\" circular empty style={{ marginLeft: \"5px\" }} />\r\n\t\t\t\t\t\t\t\t\t\t)}\r\n\t\t\t\t\t\t\t\t\t</Card.Header>\r\n\t\t\t\t\t\t\t\t\t<Card.Meta as={NoSelect}>{moment(date).format(\"YYYY-MM-DD\")}</Card.Meta>\r\n\t\t\t\t\t\t\t\t\t<Card.Description as={NoSelect}>{description}</Card.Description>\r\n\t\t\t\t\t\t\t\t</Card.Content>\r\n\t\t\t\t\t\t\t\t<Card.Content extra>\r\n\t\t\t\t\t\t\t\t\t<Button\r\n\t\t\t\t\t\t\t\t\t\tas={Link}\r\n\t\t\t\t\t\t\t\t\t\tto={path}\r\n\t\t\t\t\t\t\t\t\t\tstate={{ fromAllBlogPosts: true }}\r\n\t\t\t\t\t\t\t\t\t\tbasic\r\n\t\t\t\t\t\t\t\t\t\tcompact\r\n\t\t\t\t\t\t\t\t\t\tfloated=\"right\"\r\n\t\t\t\t\t\t\t\t\t\tcolor=\"pink\"\r\n\t\t\t\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t\t\t\tRead\r\n\t\t\t\t\t\t\t\t\t\t<Icon name=\"right chevron\" />\r\n\t\t\t\t\t\t\t\t\t</Button>\r\n\t\t\t\t\t\t\t\t\t{tags && tags.length ? (\r\n\t\t\t\t\t\t\t\t\t\t<NoSelect>\r\n\t\t\t\t\t\t\t\t\t\t\t<Label.Group>{toBlogTags(tags)}</Label.Group>\r\n\t\t\t\t\t\t\t\t\t\t</NoSelect>\r\n\t\t\t\t\t\t\t\t\t) : null}\r\n\t\t\t\t\t\t\t\t</Card.Content>\r\n\t\t\t\t\t\t\t</Card>\r\n\t\t\t\t\t\t))}\r\n\t\t\t\t\t</Responsive>\r\n\t\t\t\t</Segment>\r\n\t\t\t) : (\r\n\t\t\t\t<NoSelect>\r\n\t\t\t\t\t<Message\r\n\t\t\t\t\t\tinfo\r\n\t\t\t\t\t\tfloating\r\n\t\t\t\t\t\tsize=\"huge\"\r\n\t\t\t\t\t\ticon=\"find\"\r\n\t\t\t\t\t\theader=\"No post found\"\r\n\t\t\t\t\t\tstyle={{ backgroundColor: \"transparent\", boxShadow: \"none\" }}\r\n\t\t\t\t\t/>\r\n\t\t\t\t</NoSelect>\r\n\t\t\t)}\r\n\t\t</Body>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(Blog, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as Blog };\r\n";