import { Fragment, memo, useState, useEffect } from "react";
import { Link, useParams, useLocation, useNavigate } from "react-router-dom";
import { Helmet } from "react-helmet";
import { Button, Grid, Header, Divider, Icon, Responsive } from "semantic-ui-react";
import styled from "styled-components";
import { MarkdownLink } from "./Styles";
import { PageNotFound } from "./../PageNotFound";
import { TocMarkdown } from "./TocMarkdown";
import { LocalStorageService } from "../../../../service";
import { MarkdownContent } from "./MarkdownContent";
import { ReadingProgressBar } from "./ReadingProgressBar";
import { Body } from "../../../Styles";
import { responsiveMaxWidth, responsiveMinWidth, blogPosts } from "../../../../config";
import first from "lodash/first";
import map from "lodash/map";
import filter from "lodash/filter";
import findIndex from "lodash/findIndex";
import find from "lodash/find";
import clamp from "lodash/clamp";

const HeaderWithAnchor = styled.div`
	.header-with-anchor:not(:hover) i::before {
		// content: none !important;
		opacity: 0.2 !important;
		color: #9e9e9e;
		transition: 400ms !important;
	}

	.header-with-anchor i {
		color: #e91e63 !important;
	}

	.header-with-anchor-1 i {
		margin-left: -45px !important;
	}

	.header-with-anchor-2 i {
		margin-left: -40px !important;
	}

	.header-with-anchor-3 i {
		margin-left: -32px !important;
	}

	.header-with-anchor-4 i {
		margin-left: -28.5px !important;
	}

	.header-with-anchor-5 i {
		margin-left: -28px !important;
	}

	.header-with-anchor-6 i {
		margin-left: -22px !important;
	}
`;

const FontSizeButtonGroup = styled(Button.Group)`
	position: fixed;
	bottom: 20px;
	right: 100px;
	z-index: 1000;
	border-radius: 4px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;

	button {
		padding: 0 !important;
		width: 30px !important;
		height: 30px !important;
	}
`;

function MarkdownLinkWrapper(props) {
	const { title, path } = props;

	const [isHovered, setHovered] = useState(false);

	return (
		<MarkdownLink
			hovered={isHovered}
			onMouseEnter={() => setHovered(true)}
			onMouseOver={() => setHovered(true)}
			onMouseLeave={() => setHovered(false)}
			onTouchStart={() => setHovered(true)}
			onTouchEnd={() => setHovered(false)}
			onTouchCancel={() => setHovered(false)}
		>
			<Link className="allow-select" to={path}>
				{title}
			</Link>
		</MarkdownLink>
	);
}

function BlogPost(props) {
	const { postId } = useParams();

	const [fontSizeLevel, setFontSizeLevel] = useState(LocalStorageService.getBlogPostFontSizeLevel());

	const allBlogPosts = blogPosts.slice().reverse();
	const currentPost = find(allBlogPosts, { postId });

	let source = "";
	try {
		source = currentPost ? require(`!!raw-loader!../../../../${currentPost.src}`).default : "";
	} catch (e) {}

	const location = useLocation();
	const navigate = useNavigate();

	const backToAllBlogPostsButton = (
		<span
			onClick={() => {
				if (window.history?.length >= 2 && location?.state?.fromAllBlogPosts) {
					navigate(-1);
				} else {
					navigate("/blog");
				}
			}}
		>
			<Responsive
				as={Button}
				minWidth={responsiveMinWidth}
				basic
				compact
				color="pink"
				size="medium"
				content={
					<>
						Back to <b>All Blog Posts</b>
					</>
				}
				icon="arrow left"
				labelPosition="left"
			/>
			<Responsive maxWidth={responsiveMaxWidth}>
				<Button basic compact color="pink" size="medium" icon="arrow left" content="Back" />
			</Responsive>
		</span>
	);

	const newerPost = allBlogPosts[findIndex(allBlogPosts, (e) => e.postId === postId) - 1];
	const newerButton = (
		<>
			<Responsive
				as={Button}
				minWidth={responsiveMinWidth}
				compact
				color="violet"
				size="medium"
				content={newerPost ? newerPost.title : "Newer Post"}
				icon="arrow left"
				labelPosition="left"
				floated="right"
				style={{
					opacity: newerPost === undefined ? 0.35 : 1,
					cursor: newerPost === undefined && "not-allowed",
				}}
			/>
			<Responsive maxWidth={responsiveMaxWidth}>
				<Button
					compact
					color="violet"
					size="medium"
					icon
					floated="right"
					style={{
						opacity: newerPost === undefined ? 0.35 : 1,
						cursor: newerPost === undefined && "not-allowed",
					}}
				>
					<Icon name="arrow left" />
				</Button>
			</Responsive>
		</>
	);

	const olderPost = allBlogPosts[findIndex(allBlogPosts, (e) => e.postId === postId) + 1];
	const olderButton = (
		<>
			<Responsive
				as={Button}
				minWidth={responsiveMinWidth}
				compact
				color="purple"
				size="medium"
				content={olderPost ? olderPost.title : "Older Post"}
				icon="arrow right"
				labelPosition="right"
				floated="right"
				style={{
					opacity: olderPost === undefined ? 0.35 : 1,
					cursor: olderPost === undefined && "not-allowed",
				}}
			/>
			<Responsive maxWidth={responsiveMaxWidth}>
				<Button
					compact
					color="purple"
					size="medium"
					icon
					floated="right"
					style={{
						opacity: olderPost === undefined ? 0.35 : 1,
						cursor: olderPost === undefined && "not-allowed",
					}}
				>
					<Icon name="arrow right" />
				</Button>
			</Responsive>
		</>
	);

	const buttons = (
		<Grid>
			<Grid.Column width={6}>{backToAllBlogPostsButton}</Grid.Column>
			<Grid.Column width={10}>
				{olderPost && olderPost.path ? <Link to={olderPost.path}>{olderButton}</Link> : <>{olderButton}</>}
				{newerPost && newerPost.path ? <Link to={newerPost.path}>{newerButton}</Link> : <>{newerButton}</>}
			</Grid.Column>
		</Grid>
	);

	const [readingProgress, setReadingProgress] = useState(0);

	useEffect(() => {
		if (readingProgress >= 0.5) {
			LocalStorageService.addReadBlogPostId(postId);
		}
	}, [postId, readingProgress]);

	useEffect(() => {
		const scrollEventListener = (e) => {
			const postContentElement = document.getElementById("post-content");
			const readProgress = clamp(
				postContentElement &&
					document.scrollingElement &&
					document.documentElement &&
					(document.scrollingElement.scrollTop -
						postContentElement.offsetTop +
						document.scrollingElement.offsetHeight / 2) /
						(postContentElement.scrollHeight - document.scrollingElement.offsetHeight / 2),
				0,
				1
			);

			setReadingProgress(readProgress);
		};

		document.addEventListener("scroll", scrollEventListener);

		return () => {
			document.removeEventListener("scroll", scrollEventListener);
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	return (
		<>
			<ReadingProgressBar progress={readingProgress} />

			{currentPost && (
				<FontSizeButtonGroup floated="right">
					<Button
						color="black"
						disabled={fontSizeLevel === 0}
						onClick={() => {
							if (fontSizeLevel > 0) {
								setFontSizeLevel((prevLevel) => {
									const newLevel = prevLevel - 1;
									LocalStorageService.setBlogPostFontSizeLevel(newLevel);
									return newLevel;
								});
							}
						}}
						style={{ width: "50px", fontSize: "12px" }}
					>
						A
					</Button>
					<Button
						color="black"
						disabled={fontSizeLevel === 3}
						onClick={() => {
							if (fontSizeLevel < 3) {
								setFontSizeLevel((prevLevel) => {
									const newLevel = prevLevel + 1;
									LocalStorageService.setBlogPostFontSizeLevel(newLevel);
									return newLevel;
								});
							}
						}}
						style={{ width: "50px", fontSize: "18px" }}
					>
						A
					</Button>
				</FontSizeButtonGroup>
			)}

			<Body>
				<Helmet>
					<title>
						{currentPost ? currentPost.title : "404 Not Found"} | Blog | {process.env.REACT_APP_TITLE}
					</title>
				</Helmet>

				{currentPost ? (
					<HeaderWithAnchor>
						{buttons}
						<Divider section />

						<Header as="h1" style={{ cursor: "default" }}>
							{currentPost.title}
						</Header>

						{currentPost.previousPostId && (
							<Header as="h4" style={{ textAlign: "center", lineHeight: "1.8em" }}>
								<div style={{ cursor: "default" }}>
									<b>Continued from previous post:</b>
								</div>
								<MarkdownLinkWrapper
									title={first(
										map(filter(blogPosts, { postId: currentPost.previousPostId }), "title")
									)}
									path={first(map(filter(blogPosts, { postId: currentPost.previousPostId }), "path"))}
								/>
							</Header>
						)}

						<div
							style={{ display: "flex", justifyContent: "center", alignItems: "center", margin: "3em 0" }}
						>
							<img
								src={currentPost.thumbnail}
								alt={currentPost.thumbnail.replace(/\.(jpg|png|gif)/, "")}
								draggable={false}
								onContextMenu={(e) => e.preventDefault()}
								style={{
									width: "50%",
									maxHeight: "400px",
									objectFit: "contain",
								}}
							/>
						</div>
						<TocMarkdown markdown={source} />

						<MarkdownContent markdown={source} fontSizeLevel={fontSizeLevel} />

						{filter(blogPosts, { previousPostId: currentPost.postId }).length > 0 && (
							<Header as="h4" style={{ textAlign: "center", lineHeight: "1.8em", paddingTop: "2em" }}>
								<div style={{ cursor: "default" }}>
									<b>
										Next Post
										{filter(blogPosts, { previousPostId: currentPost.postId }).length > 1
											? "s"
											: ""}
										:
									</b>
								</div>
								{filter(blogPosts, { previousPostId: currentPost.postId }).map((post, i, arr) => (
									<Fragment key={i}>
										<MarkdownLinkWrapper title={post.title} path={post.path} />
										{i < arr.length - 1 && <br />}
									</Fragment>
								))}
							</Header>
						)}

						{source && (
							<>
								<Divider section />

								{buttons}
							</>
						)}
					</HeaderWithAnchor>
				) : (
					<PageNotFound />
				)}
			</Body>
		</>
	);
}

const memoComponent = memo(BlogPost, (prev, next) => prev.postId === next.postId);
export default memoComponent;
export { memoComponent as BlogPost };
