import { memo } from "react";
import { Message } from "semantic-ui-react";
import { NoSelect } from "../../Styles";

function PageNotFound(props) {
	return (
		<NoSelect>
			<Message
				error
				floating
				size="huge"
				icon="find"
				header="404 Not found"
				content="Sorry, we are not able to locate the webpage you are looking for."
				style={{ backgroundColor: "transparent", boxShadow: "none" }}
			/>
		</NoSelect>
	);
}

const memoComponent = memo(PageNotFound, (prev, next) => true);
export default memoComponent;
export { memoComponent as PageNotFound };
