import { memo } from "react";
import styled from "styled-components";

function getCubeStyle(index, itemCountPerRow) {
	return `
		.sk-cube${index + 1} {
			animation-delay: ${Number(0.1 * (Math.floor(index / 3) + (index % itemCountPerRow))).toFixed(1)}s;
		}
	`;
}

const LoadingIndicatorWrapper = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;

	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
`;

const LoadingIndicatorContainer = styled.div`
	position: absolute;
	width: 40px;
	height: 40px;

	.sk-cube {
		width: 33%;
		height: 33%;
		background-color: rgba(255, 87, 34, 0.75);
		float: left;
		transform: scale3d(0, 0, 1);
		animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
	}

	${Array(9)
		.fill(null)
		.map((v, i) => getCubeStyle(i, 3))
		.join("")}

	@keyframes sk-cubeGridScaleDelay {
		0%,
		100% {
			transform: scale3d(0, 0, 1);
		}
		35%,
		70% {
			transform: scale3d(1, 1, 1);
		}
	}
`;

function LoadingIndicator(props) {
	return (
		<LoadingIndicatorWrapper>
			<LoadingIndicatorContainer>
				<div className="sk-cube sk-cube1"></div>
				<div className="sk-cube sk-cube2"></div>
				<div className="sk-cube sk-cube3"></div>
				<div className="sk-cube sk-cube4"></div>
				<div className="sk-cube sk-cube5"></div>
				<div className="sk-cube sk-cube6"></div>
				<div className="sk-cube sk-cube7"></div>
				<div className="sk-cube sk-cube8"></div>
				<div className="sk-cube sk-cube9"></div>
			</LoadingIndicatorContainer>
		</LoadingIndicatorWrapper>
	);
}

const memoComponent = memo(LoadingIndicator, (prev, next) => true);
export default memoComponent;
export { memoComponent as LoadingIndicator };
