export default "import { memo, useState } from \"react\";\r\nimport { Header, Icon } from \"semantic-ui-react\";\r\nimport styled from \"styled-components\";\r\n\r\nconst ContactItemWrapper = styled(({ hovered, ...props }) => <div {...props} />)`\r\n\t.contact-item {\r\n\t\ttransition: 250ms;\r\n\t\topacity: ${({ hovered }) => (hovered ? 1 : 0.75)};\r\n\t\t${({ hovered }) => (hovered ? \"transform: scale(1.05);\" : \"\")}\r\n\t}\r\n`;\r\n\r\nfunction ContactItem(props) {\r\n\tconst { href, icon, color, iconRotation, header, subHeader } = props;\r\n\tconst isUrl = new RegExp(\"^https?://\", \"i\").test(href);\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t// eslint-disable-next-line react/jsx-no-target-blank\r\n\t\t<a href={href} target={isUrl ? \"_blank\" : null} rel={isUrl ? \"external nofollow noopener noreferrer\" : null}>\r\n\t\t\t<ContactItemWrapper hovered={isHovered}>\r\n\t\t\t\t<Header\r\n\t\t\t\t\tclassName=\"contact-item\"\r\n\t\t\t\t\tas=\"h4\"\r\n\t\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\t\tonClick={() => setHovered(false)}\r\n\t\t\t\t>\r\n\t\t\t\t\t<Icon name={icon} color={color} rotated={iconRotation} />\r\n\t\t\t\t\t<Header.Content>\r\n\t\t\t\t\t\t{header}\r\n\t\t\t\t\t\t<Header.Subheader>{subHeader}</Header.Subheader>\r\n\t\t\t\t\t</Header.Content>\r\n\t\t\t\t</Header>\r\n\t\t\t</ContactItemWrapper>\r\n\t\t</a>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(ContactItem, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as ContactItem };\r\n";