import styled from "styled-components";

const MarkdownLink = styled(({ hovered, ...props }) => <span {...props} />)`
	a {
		color: ${({ hovered }) => (hovered ? "#3f51b5" : "#0288d1")} !important;
		transition: 250ms;
		position: relative;
		font-weight: bold;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: ${({ hovered }) => (hovered ? 100 : 0)}%;
			height: 1px;
			background: #3f51b5;
			transition: 250ms;
		}
	}
`;

const exports = { MarkdownLink };
export default exports;
export { MarkdownLink };
