export default "import { memo, useState } from \"react\";\r\nimport { Link } from \"react-router-dom\";\r\nimport { Segment, Header } from \"semantic-ui-react\";\r\nimport styled from \"styled-components\";\r\nimport { NoSelect } from \"../../../Styles\";\r\n\r\nconst CircleShortcutWrapper = styled(\r\n\t({ themeColor, hovered, hoverBg, normalBg, hoverTextColor, normalTextColor, glowEffectBg, ...props }) => (\r\n\t\t<div {...props} />\r\n\t)\r\n)`\r\n\t.circle-shortcut {\r\n\t\twidth: 150px;\r\n\t\theight: 150px;\r\n\t\tmin-width: 150px;\r\n\t\tmin-height: 150px;\r\n\t\ttransition: 250ms;\r\n\t\tborder: none;\r\n\t\tbackground-color: ${({ hovered, hoverBg, normalBg }) => (hovered ? hoverBg : normalBg)} !important;\r\n\t\tbox-shadow: 0 0 30px ${({ glowEffectBg }) => glowEffectBg} !important;\r\n\t\t${({ hovered }) => (hovered ? \"transform: scale(0.95);\" : \"\")}\r\n\t}\r\n\r\n\t.circle-shortcut-macaron {\r\n\t\twidth: 150px;\r\n\t\theight: 165px;\r\n\t\tmin-width: 150px;\r\n\t\tmin-height: 165px;\r\n\t\ttransition: 250ms;\r\n\t\tborder: none;\r\n\t\tbox-shadow: none;\r\n\t\tbackground-color: transparent;\r\n\t\tbackground-image: url(\"macaron-${({ themeColor }) => themeColor}.png\");\r\n\t\tbackground-image: -webkit-image-set(\r\n\t\t\turl(\"macaron-${({ themeColor }) => themeColor}.webp\") 1x,\r\n\t\t\turl(\"macaron-${({ themeColor }) => themeColor}-2x.webp\") 2x\r\n\t\t);\r\n\t\tbackground-size: 98%;\r\n\t\tbackground-repeat: no-repeat;\r\n\t\tbackground-position: center;\r\n\t\t${({ hovered }) => (hovered ? \"transform: scale(1.15);\" : \"\")}\r\n\t}\r\n\r\n\t.circle-shortcut h3 {\r\n\t\ttransition: 250ms;\r\n\t\tcolor: ${({ hovered, normalTextColor, hoverTextColor }) =>\r\n\t\t\thovered ? hoverTextColor : normalTextColor} !important;\r\n\t}\r\n\r\n\t.circle-shortcut-macaron h3 {\r\n\t\ttransition: 250ms;\r\n\t\tmargin-top: -1.5em !important;\r\n\t\tcolor: ${({ normalTextColor }) => normalTextColor} !important;\r\n\t}\r\n\r\n\t@media (hover: hover) {\r\n\t\t.circle-shortcut,\r\n\t\t.circle-shortcut-macaron {\r\n\t\t\t-webkit-box-reflect: below 0em -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(255, 255, 255, 0.1)));\r\n\t\t}\r\n\r\n\t\t.circle-shortcut-macaron:hover {\r\n\t\t\tfilter: drop-shadow(0 0 20px ${({ glowEffectBg }) => glowEffectBg});\r\n\t\t}\r\n\t}\r\n`;\r\n\r\nfunction CircleShortcut(props) {\r\n\tconst {\r\n\t\tlinkTo,\r\n\t\ttext,\r\n\t\tthemeColor,\r\n\t\tnormalColor: normalTextColor,\r\n\t\thoverColor: hoverTextColor,\r\n\t\tnormalBg,\r\n\t\thoverBg,\r\n\t\tglowEffectBg,\r\n\t} = props;\r\n\r\n\tconst [isHovered, setHovered] = useState(false);\r\n\r\n\treturn (\r\n\t\t<NoSelect style={{ display: \"inline-block\" }}>\r\n\t\t\t<CircleShortcutWrapper\r\n\t\t\t\tthemeColor={themeColor}\r\n\t\t\t\thoverBg={hoverBg}\r\n\t\t\t\thoverTextColor={hoverTextColor}\r\n\t\t\t\tnormalBg={normalBg}\r\n\t\t\t\tnormalTextColor={normalTextColor}\r\n\t\t\t\tglowEffectBg={glowEffectBg}\r\n\t\t\t\thovered={isHovered}\r\n\t\t\t>\r\n\t\t\t\t<Link to={linkTo} draggable={false}>\r\n\t\t\t\t\t<Segment\r\n\t\t\t\t\t\tclassName=\"circle-shortcut-macaron\"\r\n\t\t\t\t\t\tcircular\r\n\t\t\t\t\t\tonMouseEnter={() => setHovered(true)}\r\n\t\t\t\t\t\tonMouseOver={() => setHovered(true)}\r\n\t\t\t\t\t\tonMouseLeave={() => setHovered(false)}\r\n\t\t\t\t\t\tonTouchStart={() => setHovered(true)}\r\n\t\t\t\t\t\tonTouchEnd={() => setHovered(false)}\r\n\t\t\t\t\t\tonTouchCancel={() => setHovered(false)}\r\n\t\t\t\t\t>\r\n\t\t\t\t\t\t<Header as=\"h3\" style={{ textTransform: \"uppercase\" }}>\r\n\t\t\t\t\t\t\t{text || \"\"}\r\n\t\t\t\t\t\t</Header>\r\n\t\t\t\t\t</Segment>\r\n\t\t\t\t</Link>\r\n\t\t\t</CircleShortcutWrapper>\r\n\t\t</NoSelect>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(CircleShortcut, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as CircleShortcut };\r\n";