export default "import { memo, useState } from \"react\";\r\nimport { Link, useLocation } from \"react-router-dom\";\r\nimport { Container, Menu, Dropdown, Responsive } from \"semantic-ui-react\";\r\nimport { NameAndRole, Name, Role } from \"./Styles\";\r\nimport { Body } from \"./../Styles\";\r\nimport { responsiveMinWidth } from \"../../config\";\r\n\r\nfunction SiteHeader(props) {\r\n\tconst [openExp, setOpenExp] = useState(false);\r\n\r\n\tconst location = useLocation();\r\n\tconst currentPath = location.pathname;\r\n\r\n\tconst linkPath = (to) => ({\r\n\t\t...(currentPath === to ? {} : { as: Link, to }),\r\n\t\tactive: currentPath === to,\r\n\t\tstyle: {\r\n\t\t\ttextAlign: \"center\",\r\n\t\t\tpointerEvents: currentPath === to && \"none\",\r\n\t\t},\r\n\t});\r\n\r\n\treturn (\r\n\t\t<div className=\"site-header\">\r\n\t\t\t<NameAndRole>\r\n\t\t\t\t<Name>Chung Cheuk Hang Michael</Name>\r\n\t\t\t\t<Role>Web Developer</Role>\r\n\t\t\t</NameAndRole>\r\n\r\n\t\t\t<Responsive as={Body} minWidth={responsiveMinWidth} style={{ paddingBottom: \"1em\" }}>\r\n\t\t\t\t<Container text textAlign=\"center\">\r\n\t\t\t\t\t<Menu stackable borderless compact widths={5} size=\"tiny\" inverted>\r\n\t\t\t\t\t\t<Menu.Item name=\"Home\" color=\"blue\" {...linkPath(\"/\")} draggable={false} />\r\n\t\t\t\t\t\t<Dropdown\r\n\t\t\t\t\t\t\titem\r\n\t\t\t\t\t\t\tsimple\r\n\t\t\t\t\t\t\ttext=\"My Experiences\"\r\n\t\t\t\t\t\t\topen={openExp}\r\n\t\t\t\t\t\t\tonMouseEnter={() => setOpenExp(true)}\r\n\t\t\t\t\t\t\tonMouseLeave={() => setOpenExp(false)}\r\n\t\t\t\t\t\t>\r\n\t\t\t\t\t\t\t<Dropdown.Menu>\r\n\t\t\t\t\t\t\t\t<Dropdown.Item {...linkPath(\"/workExp\")} draggable={false}>\r\n\t\t\t\t\t\t\t\t\tWork\r\n\t\t\t\t\t\t\t\t</Dropdown.Item>\r\n\t\t\t\t\t\t\t\t<Dropdown.Item {...linkPath(\"/hobbyProject\")} draggable={false}>\r\n\t\t\t\t\t\t\t\t\tHobby Projects\r\n\t\t\t\t\t\t\t\t</Dropdown.Item>\r\n\t\t\t\t\t\t\t</Dropdown.Menu>\r\n\t\t\t\t\t\t</Dropdown>\r\n\r\n\t\t\t\t\t\t<Menu.Item name=\"Personality\" color=\"purple\" {...linkPath(\"/personality\")} draggable={false} />\r\n\t\t\t\t\t\t<Menu.Item name=\"Blog\" color=\"teal\" {...linkPath(\"/blog\")} draggable={false} />\r\n\t\t\t\t\t\t<Menu.Item name=\"Contact\" color=\"yellow\" {...linkPath(\"/contact\")} draggable={false} />\r\n\t\t\t\t\t</Menu>\r\n\t\t\t\t</Container>\r\n\t\t\t</Responsive>\r\n\t\t</div>\r\n\t);\r\n}\r\n\r\nconst memoComponent = memo(SiteHeader, (prev, next) => true);\r\nexport default memoComponent;\r\nexport { memoComponent as SiteHeader };\r\n";