import { memo, useState } from "react";
import { Helmet } from "react-helmet";
import { Header, Button, Form, Message, Icon, Modal, Transition } from "semantic-ui-react";
import styled from "styled-components";
import { Body, NoSelect } from "../../Styles";
import { GradientButton } from "../../reusable/GradientButton";
import { CommentService, NetworkService } from "../../../service";
import get from "lodash/get";
import mapValues from "lodash/mapValues";

const errorMessageMap = mapValues(
	{
		1: {
			header: "Missing email/phone",
			content: "You must enter either your email or phone number.",
		},
		2: {
			header: "Missing message",
			content: "You must enter a message.",
		},
		3: {
			header: "Invalid email address",
			content: "The format of the email address you enter is invalid.",
		},
		4: {
			header: "Invalid phone number",
			content: "The format of the phone number you enter is invalid.",
		},
	},
	(v) => ({
		...v,
		content: v.content + " Please double-check your input.",
		headerIcon: "ban",
		buttonIcon: "write",
		button: "Try again",
	})
);

const ContactFormWrapper = styled.div`
	input,
	textarea {
		background-color: rgba(255, 255, 255, 0.35) !important;
		color: #8e24aa !important;
	}

	form::before {
		background-color: transparent !important;
	}
`;

function Contact(props) {
	const [firstName, setFirstName] = useState("");
	const [lastName, setLastName] = useState("");
	const [phone, setPhone] = useState("");
	const [email, setEmail] = useState("");
	const [message, setMessage] = useState("");
	const [isSendButtonClicked, setSendButtonClicked] = useState(false);
	const [isSent, setSent] = useState(0); // 0 = not sent, 1 = sending, 2 = sent
	const [isErrorPopupOpen, setErrorPopupOpen] = useState(false);

	const errorMissingEmailOrPhone =
		!!email || !!phone
			? false
			: isSendButtonClicked
			? {
					content: "You must enter your email and/or phone no.",
					pointing: "above",
			  }
			: true;
	const errorMissingMessage = !!message
		? false
		: isSendButtonClicked
		? {
				content: "You must leave a message.",
				pointing: "above",
		  }
		: true;
	const errorInvalidEmail =
		!!email && !new RegExp("^\\w+@\\w+\\.\\w+$").test(email)
			? {
					content: "You must enter a valid email.",
					pointing: "above",
			  }
			: false;
	const errorInvalidPhone =
		!!phone && !new RegExp("^(\\+?852)?\\d{8}$").test(phone.replace(/\s/g, ""))
			? {
					content: "You must enter a valid HK phone no.",
					pointing: "above",
			  }
			: false;
	const errorMessageType = errorMissingEmailOrPhone
		? 1
		: errorMissingMessage
		? 2
		: errorInvalidEmail
		? 3
		: errorInvalidPhone
		? 4
		: 0;
	const isValidInput = errorMessageType === 0;

	const sendToFirebase = () => {
		NetworkService.getClientIp()
			.then((response) => {
				if (!response || !response.data) {
					return;
				}

				const clientIp = (response.data.split("\n").filter((e) => e.startsWith("ip="))[0] || "").replace(
					/^ip=/,
					""
				);

				CommentService.postComment({
					firstName,
					lastName,
					phone,
					email,
					message,
					clientIp,
				})
					.then((response) => {
						setSent(2);
						console.log("Successfully sent.");
					})
					.catch((error) => {
						setSent(0);
						alert("Failed to send (2).");
						console.log(error);
					});
			})
			.catch((error) => {
				setSent(0);
				alert("Failed to send (1).");
				console.log(error);
			});
	};

	return (
		<Body>
			<Helmet>
				<title>Contact | {process.env.REACT_APP_TITLE}</title>
			</Helmet>

			<Header size="huge" style={{ cursor: "default" }}>
				Contact
			</Header>

			<Transition visible={isErrorPopupOpen} animation="scale" duration={400}>
				<Modal open={isErrorPopupOpen}>
					<Header
						icon={get(errorMessageMap[errorMessageType], "headerIcon", "")}
						content={<NoSelect>{get(errorMessageMap[errorMessageType], "header", "")}</NoSelect>}
					/>
					<Modal.Content>
						<NoSelect>{get(errorMessageMap[errorMessageType], "content", "")}</NoSelect>
					</Modal.Content>
					<Modal.Actions>
						<Button color="red" onClick={() => setErrorPopupOpen(false)}>
							<Icon name={get(errorMessageMap[errorMessageType], "buttonIcon", "")} />{" "}
							{get(errorMessageMap[errorMessageType], "button", "")}
						</Button>
					</Modal.Actions>
				</Modal>
			</Transition>

			{isSent === 2 ? (
				<NoSelect>
					<Message
						info
						floating
						size="huge"
						icon
						style={{ backgroundColor: "transparent", boxShadow: "none" }}
					>
						<span style={{ fontSize: "3em" }}>👌</span>
						<Message.Content>
							<Message.Header>Successfully Sent</Message.Header>
							Appreciate your time, I'll get back to you soon, cheers!
						</Message.Content>
					</Message>
				</NoSelect>
			) : (
				<>
					<ContactFormWrapper>
						<Form size="big" loading={isSent > 0}>
							<Form.Group widths="equal">
								<Form.Input
									fluid
									label="First name"
									placeholder="First name (optional)"
									value={firstName}
									disabled={isSent > 0}
									onChange={(e) => setFirstName(e.target.value)}
								/>
								<Form.Input
									fluid
									label="Last name"
									placeholder="Last name (optional)"
									value={lastName}
									disabled={isSent > 0}
									onChange={(e) => setLastName(e.target.value)}
								/>
							</Form.Group>
							<Form.Group widths="equal">
								<Form.Input
									fluid
									label="Email"
									placeholder="example@gmail.com"
									required
									error={errorMissingEmailOrPhone || errorInvalidEmail}
									value={email}
									disabled={isSent > 0}
									onChange={(e) => setEmail(e.target.value)}
								/>
								<Form.Input
									fluid
									label="Phone Number"
									placeholder="Mine: 6330 1333"
									required
									error={errorMissingEmailOrPhone || errorInvalidPhone}
									value={phone}
									disabled={isSent > 0}
									onChange={(e) => setPhone(e.target.value)}
								/>
							</Form.Group>
							<Form.TextArea
								rows={8}
								label="Message"
								placeholder="Leave me a message :)"
								required
								error={errorMissingMessage}
								value={message}
								disabled={isSent > 0}
								onChange={(e) => setMessage(e.target.value)}
							/>
						</Form>
					</ContactFormWrapper>
					<div style={{ display: "flex", justifyContent: "center", alignItems: "center", marginTop: "2em" }}>
						<GradientButton
							capitalize
							style={{ ...(isSent > 0 && { opacity: 0.5, pointerEvents: "none" }) }}
							action={
								isSent > 0
									? null
									: () => {
											setSendButtonClicked(true);
											if (!isValidInput) {
												setErrorPopupOpen(true);
												return;
											}
											setSent(1);
											sendToFirebase();
									  }
							}
						>
							Send
						</GradientButton>
					</div>
				</>
			)}
		</Body>
	);
}

const memoComponent = memo(Contact, (prev, next) => true);
export default memoComponent;
export { memoComponent as Contact };
